/* ----------------------------------------------------------------
    BODY
-----------------------------------------------------------------*/

html {
  overflow-x: hidden !important;
}

body {
  color: $color-dk-gray;
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  font-size: 18px;
  line-height: 1.7;
  font-weight: 300;
  overflow: hidden;
  padding: 0 !important;
  position: relative;
  margin: 0.0;
}

p, li  {
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  line-height: 1.5;
  font-weight: 300;
  letter-spacing: .25px;
}

li {
  margin-bottom: 20px;
}

/* ----------------------------------------------------------------
    LAYOUT BOXED & WIDE
-----------------------------------------------------------------*/

@media (max-width: 479px) {
  .container {
    padding: 0 20px;
  }
}

/*Wide (default)*/

body.wide .wrapper {
  width: 100%;
  margin: 0;
}

/*Boxed*/

body.boxed .wrapper,
body.boxed-simple .wrapper {
  position: relative;
  border: 1px solid $color-lt-gray;
  background-color: $color-white;
}

body.outter {
  margin: 30px;
}

body.boxed-simple {
  background-color: $color-md-gray;
  border: 0 none !important;
}

/* Responsive */

@media (max-width: 479px) {
  body.outter {
    margin: 0;
  }
}

a {
  color: $color-md-lime-green;
  text-decoration: none !important;
  &:hover {
    color: $color-dk-leaf-green;
  }
}

a:focus {
  outline: none;
}

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}

iframe {
  border: 1px solid $color-dk-gray;
}
img {
  max-width: 100%;
  height: auto;
}

/* ----------------------------------------------------------------
    TYPOGRAPHY HEADING STYLES
-----------------------------------------------------------------*/

h1, h2, h3, h5, h6 {
  color: $color-dk-purple;
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
  line-height: 1.5;
  margin: 0 0 10px 0;
  text-rendering: optimizelegibility;
}

h1 a, h2 a, h3 a, h5 a, h6 a {
  color: $color-md-purple;
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
}

h1.xl {
  font-size: 70px;
  line-height: 1.5;
}

h1, .jumbotron h1, .custom-heading h1 {
  font-size: 56px;
  line-height: 1.2;
}

h2, .custom-heading h2 {
  font-size: 40px;
  line-height: 1.5;
}

h3, .icon-box.small > h3, .post-title h3 {
  font-size: 24px;
  font-weight: 600;
}

.custom-heading h3 {
  font-family: Roboto;
  font-weight: 100;
  font-style: normal;
  font-size: 20px;
}

h4, .footer-content .widget-title, .custom-heading h4 {
  color: $color-dk-purple;
  font-size: 18px;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  letter-spacing: .25px;
  line-height: 1.5;
  margin: 0 0 10px 0;
  text-rendering: optimizelegibility;
  a {
    color: $color-md-leaf-green;
  }
}

h5, .custom-heading h5 {
  font-size: 24px;
}

h6, .custom-heading h6 {
  font-size: 16px;
}

h5,
h6 {
  font-weight: 300;
  margin-bottom: 0px;
  color: $color-dk-purple;
}

p,
p.lead  {
  color: $color-dk-gray;
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  line-height: 1.7;
  font-weight: 300;
  letter-spacing: .25px;
  margin-bottom: 30px;
}

.sm p, p.sm {
  font-size: 12px;
  font-weight: 300;
  margin: 0px;
}

/* ----------------------------------------------------------------
    WORDPRESS CORE
-----------------------------------------------------------------*/
/*4.1 Align and images */
.alignnone {
  margin: 5px 20px 20px 0;
}

.aligncenter,
div.aligncenter {
  display: block;
  margin: 5px auto 5px auto;
}

.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}

.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}

a img.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}

a img.alignnone {
  margin: 5px 20px 20px 0;
}

a img.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}

a img.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto
}

.wp-caption {
  background: $color-white;
  border: 1px solid $color-lt-gray;
  max-width: 96%; /* Image does not overflow the content area */
  padding: 5px 3px 5px;
  text-align: center;
  font-size: .8em;
}

.wp-caption.alignnone {
  margin: 5px 20px 20px 0;
}

.wp-caption.alignleft {
  margin: 5px 20px 20px 0;
}

.wp-caption.alignright {
  margin: 5px 0 20px 20px;
}

.wp-caption img {
  border: 0 none;
  height: auto;
  margin: 0 0 7px 0;
  max-width: 98.5%;
  padding: 0;
  width: auto;
}

.wp-caption p.wp-caption-text {
  font-size: 11px;
  line-height: 17px;
  margin: 0;
  padding: 0 4px 5px;
}
/* Text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px $color-dk-purple;
  clip: auto !important;
  color: $color-md-robin-blue;
  display: block;
  font-size: 14px;
  font-weight: bold;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000; /* Above WP toolbar. */
}

.wp-video, video.wp-video-shortcode, .mejs-overlay.load {
  width: 100% !important;
  height: 100% !important;
}

.wp-video, video.wp-video-shortcode {
  max-width: 100% !important;
}

.wp-video {
  position: relative;
}

.mejs-mediaelement {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.mejs-overlay-play {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: auto !important;
  height: auto !important;
}

/**
 * 14.2 Galleries
 */

.gallery {
  margin-bottom: 1.6em;
}

.gallery-item {
  display: inline-block;
  padding: 0;
  text-align: center;
  vertical-align: top;
  width: 100%;
}

.gallery-columns-2 .gallery-item {
  max-width: 50%;
}

.gallery-columns-3 .gallery-item {
  max-width: 33.33%;
}

.gallery-columns-4 .gallery-item {
  max-width: 25%;
}

.gallery-columns-5 .gallery-item {
  max-width: 20%;
}

.gallery-columns-6 .gallery-item {
  max-width: 16.66%;
}

.gallery-columns-7 .gallery-item {
  max-width: 14.28%;
}

.gallery-columns-8 .gallery-item {
  max-width: 12.5%;
}

.gallery-columns-9 .gallery-item {
  max-width: 11.11%;
}

.gallery-icon img {
  margin: 0 auto;
}

.gallery-caption {
  color: $color-dk-gray;
  color: $color-dk-purple;
  display: block;
  font-family: "Segoe UI","Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 1.2rem;
  line-height: 1.5;
  padding: 0.5em 0;
}

.gallery-columns-6 .gallery-caption,
.gallery-columns-7 .gallery-caption,
.gallery-columns-8 .gallery-caption,
.gallery-columns-9 .gallery-caption {
  display: none;
}

.bypostauthor{}

.list-tweets-date {
  display: block;
  opacity: .8;
  font-size: 80%;
}

#footer.background-dark a:not(.button) {
  color: $color-white !important;
}

body.side-panel-static .vc_row[data-vc-full-width].vc_row[data-vc-stretch-content] > .wpb_column.vc_column_container {
  padding-left: 320px;
}

@media (max-width: 991px) {
  body.side-panel-static .vc_row[data-vc-full-width].vc_row[data-vc-stretch-content] > .wpb_column.vc_column_container {
    padding-left: 0;
  }
}

.post-comments .likely {
  position: relative;
  top: -5px;

}

.post-comments .likely .likely__widget {
  background: none;
}
/* hold for now - makes social icons vertical in desktop mode
.likely .likely__widget {
  display: block;
  text-align: left;
}
*/
.post-modern .post-comments .likely {
  top: 5px;
}

/* ----------------------------------------------------------------
	Logo
-----------------------------------------------------------------*/

#logo {
  float: left;
  font-size: 28px;
  line-height: 100%;
  position: relative;
  z-index: 1;
  flex: 1 0 auto;
}

#logo a {
  display: block;
}

#logo img {
  display: block;
  max-width: 100%;
  margin: 0 auto;
  max-height: 50px;
}

#logo .tagline {
  display: none;
}

#logo .logo-img-wrap {
  width: auto !important;
}

#logo a.logo {
  display: block;
}

.main-menu #logo {
  margin-left: 40px;
  margin-right: 40px;
}

/* ----------------------------------------------------------------
    HEADER
-----------------------------------------------------------------*/

#header {
  position: relative;
  background-color: $color-white;
  border-bottom: 1px solid $color-md-gray;
  z-index: 199 !important;
}

#header .container {
  position: relative;
}

#header.header-transparent + #slider,
#header.header-transparent + .page-title-extended,
#header.header-transparent + .page-title-video,
#header.header-transparent + .page-title-parallax,
#header.header-transparent + .fullscreen,
#header.header-transparent + .fullscreen,
#header.header-transparent + .halfscreen,
#header.header-transparent + #google-map,
#header.header-light-transparent + #slider,
#header.header-light-transparent + .page-title-extended,
#header.header-light-transparent + .page-title-video,
#header.header-light-transparent + .page-title-parallax,
#header.header-light-transparent + .fullscreen,
#header.header-light-transparent + .fullscreen,
#header.header-light-transparent + .halfscreen,
#header.header-light-transparent + #google-map,
#header.header-dark-transparent + #slider,
#header.header-dark-transparent + .page-title-extended,
#header.header-dark-transparent + .page-title-video,
#header.header-dark-transparent + .page-title-parallax,
#header.header-dark-transparent + .fullscreen,
#header.header-dark-transparent + .fullscreen,
#header.header-dark-transparent + .halfscreen,
#header.header-dark-transparent + #google-map {
  top: -90px;
  margin-bottom: -90px;
}


#page-title.page-title-parallax {background-size: cover;}

#header-wrap {
  position: relative;
  -webkit-backface-visibility: hidden;
  height: auto !important;
  display: flex;
}
#header-wrap>.container {
  display: flex;
  flex-direction: row;
}

/* ----------------------------------------------------------------
    NAVIGATION MENU
-----------------------------------------------------------------*/

.navigation-wrap {
  padding: 0;
  flex: 1 0 auto;
}

.navigation-wrap > .container {
  padding: 0 !important;
  width: 100%;
}

#mainMenu {
  float: right;
  display: table;
}

#mainMenu > ul {
  list-style: outside none none;
  margin-bottom: 0;
  padding-left: 0;
  display: table-cell;
  vertical-align: middle
}

#mainMenu > ul > li:last-child > a {
  border-bottom: 0 !important;
}

#mainMenu > ul > li > a {
  display: block;
  padding: 10px 15px;
  position: relative;
}

#mainMenu > ul > li > a:hover,
#mainMenu > ul > li > a:focus {
  background-color: $color-lt-gray;
  text-decoration: none;
}

/*labels*/

#mainMenu .label {
  font-size: 9px;
}

/*Menu items */

nav .main-menu a,
nav .main-menu .dropdown-menu a,
nav .main-menu .dropdown-submenu > span.dropdown-menu-title-only {
  color: #1e1e1f;
}

nav .main-menu .orange-dark a,
nav .main-menu .dropdown-menu .orange-dark a {
  color: $color-white;
}


#mainMenu > ul > li > a {
  border-bottom: 0 none;
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  text-transform: capitalize;
  height: 75px;
  line-height: 75px;
  margin-left: 0;
  margin-right: 0;
  outline: medium none;
  padding: 10px;
  transition: all 0.3s ease-in-out 0s;
}
#mainMenu > ul > li.current-menu-parent > a,
#mainMenu > ul > li .dropdown-menu li.current-menu-item > a {
  color: $color-lt-lime-green !important;
}

li.align-right {
  float: right;
  .visible-desktop {
    display: inline;
    padding-left: 5px;
  }
}


#mainMenu > ul > li > a.full-rounded {
  text-transform: none;
  border-radius: 50px;
  height: 40px;
  line-height: 20px;
  margin-top: 25px;
  font-size: 12px;
  color: $color-white !important;
}

#mainMenu > ul > li > a.full-rounded:hover {
  background: $color-md-orange !important;
  color: $color-white !important;
}

nav .main-menu li > a:hover,
nav .main-menu li > a:focus,
nav .main-menu .dropdown.open > a:hover,
nav .main-menu .dropdown.open > a:focus {
  background: none !important;
  border-color: transparent !important;
}

nav ul.main-menu .dropdown > .dropdown-menu,
nav ul.main-menu .dropdown-submenu > .dropdown-menu {
  visibility: hidden;
  display: block;
  opacity: 0;
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}

nav ul.main-menu .dropdown:hover > .dropdown-menu,
nav ul.main-menu .dropdown-submenu:hover > .dropdown-menu {
  opacity: 1;
  visibility: visible;
}

nav ul.main-menu .dropdown-menu li {
  margin-bottom: 0;
}
nav ul.main-menu .dropdown-menu li:last-child {
  margin-bottom: 0;
  border-bottom: 0!important;
}

nav.main-menu ul li i {
  padding-left: 3px;
  font-size: 14px;
  margin-right: 3px;
  text-align: center;
  width: 22px;
}
nav.main-menu ul li.translate-dropdown i {
  font-size: 15px;
}

nav.main-menu ul li.translate-dropdown.dropdown .dropdown-menu a:hover,
nav.main-menu ul li.translate-dropdown.dropdown .dropdown-submenu > span.dropdown-menu-title-only:hover {
  padding: 10px 10px 10px 10px;
}
nav.main-menu ul li.translate-dropdown.dropdown .dropdown-menu li,
nav.main-menu ul li.translate-dropdown.dropdown .dropdown-submenu li {
  margin-bottom: 0;
}
#mainMenu > ul > li.translate-dropdown.dropdown > a i {
  line-height: unset;
  transition: font-size .3s ease-in-out;
  -moz-transition: font-size .3s ease-in-out;
  -webkit-transition: font-size .3s ease-in-out;
}
#mainMenu > ul > li.translate-dropdown.dropdown:hover i,
#mainMenu > ul > li.translate-dropdown.dropdown > a:focus i {
  /*font-size: 24px;*/
}
#mainMenu > ul > li.translate-dropdown.dropdown>a span {
  display: inline;
  padding-left: 5px;
}
nav.main-menu ul li.dropdown > a > i {
  margin-right: 0;
}

/*Menu dropdown menu items */

nav .main-menu .dropdown-menu {
  min-width: 38px;
  top: auto;
  background-color: $color-white;
  box-shadow: 0 5px 25px 5px rgba(58, 48, 66, 0.28);
  padding: 0;
  border-style: solid none none;
  border-width: 3px 0 0;
  border-radius: 0;
  margin-left: 10px;
}

nav .main-menu .dropdown-menu a,
nav .main-menu .dropdown-submenu > span.dropdown-menu-title-only {
  font-size: 13px !important;
  font-weight: 300;
  white-space: normal;
  height: auto;
  padding: 10px 10px 10px 10px;
  position: relative;
  text-transform: none;
  -webkit-transition: padding .3s ease-in-out;
  transition: padding .3s ease-in-out;
}

nav .main-menu .dropdown-menu a:hover,
nav .main-menu .dropdown-menu span.dropdown-menu-title-only:hover {
  padding: 10px 10px 10px 15px;
}

.mobile-sticky nav .main-menu .dropdown-menu a:hover,
.mobile-sticky nav .main-menu .dropdown-menu span.dropdown-menu-title-only:hover {
  background-color: #1e1e1f !important;
  color: $color-lt-lime-green;
  padding: 10px 10px 10px 15px;
}

nav .main-menu .read-more {
  border-top: 1px solid $color-lt-gray;
  margin-bottom: -10px;
  margin-top: 24px;
}

nav .main-menu .dropdown-menu a.read-more:hover {
  background: #1e1e1f !important;
}

nav .main-menu .dropdown-menu .mega-menu-content .col-md-2 {
  padding-right: 1px;
}

nav .main-menu .dropdown-menu .mega-menu-content a {
  border-bottom: medium none;
  padding: 10px 0;
}

nav .main-menu .dropdown-menu .mega-menu-content a:hover {
  padding: 10px 0 10px 12px;
}

nav .main-menu .dropdown-menu i {
  margin-left: -2px;
  padding-right: 6px;
}

/*Menu dropdown submenu items */

nav .main-menu .dropdown-submenu {
  position: relative;
}

nav .main-menu .dropdown-submenu:before {
  content: "\f105";
  font-family: fontawesome;
  font-size: 12px;
  opacity: 0.6;
  position: absolute;
  right: 12px;
  top: 6px;
}

nav .main-menu .dropdown-submenu > .dropdown-menu {
  border-top-width: 3px;
  left: 100%;
  top: -3px;
}

nav .main-menu .dropdown-submenu > .dropdown-menu.menu-invert {
  right: 100%;
  left: auto;
}

/* Mega Menu */

nav.mega-menu .nav,
nav.mega-menu .dropup,
nav.mega-menu .dropdown,
nav.mega-menu .collapse {
  position: static;
}

nav .main-menu li.mega-menu-item .dropdown-menu {
  left: 15px;
  right: 15px;
}

nav.mega-menu li.mega-menu-item .mega-menu-title {
  display: block;
  font-size: 12px;
  font-style: normal;
  font-weight: 800;
  height: 50px;
  line-height: 32px;
  margin-left: 0;
  margin-right: 0;
  padding: 10px 0;
  text-transform: uppercase;
}

nav.mega-menu li ul {
  list-style: none;
  padding: 0;
}

nav.mega-menu .dropdown-menu a,
nav.mega-menu .dropdown-submenu > span.dropdown-menu-title-only {
  text-transform: none;
  display: block;
  text-decoration: none;
}

nav.mega-menu .dropdown-submenu > span.dropdown-menu-title-only {
  line-height: 18px;
}

nav.mega-menu .dropdown-submenu > span.dropdown-menu-title-only:hover {
  cursor: pointer;
}

nav.mega-menu .dropdown-menu {
  left: auto;
  margin-top: 0;
  max-width: 190px;
}

nav.mega-menu .dropdown-menu > li {
  display: block
}

nav.mega-menu .mega-menu-content {
  padding: 30px 40px 40px;
}

nav.mega-menu .mega-menu-content:before {
  display: table;
  content: "";
  line-height: 0;
}

nav.mega-menu .mega-menu-content:after {
  display: table;
  content: "";
  line-height: 0;
  clear: both;
}

nav.mega-menu .mega-menu-content {
  background-position: right bottom;
  background-repeat: no-repeat;
}

#mainMenu ul li.mega-menu-item .mega-menu-title,
#mainMenu ul li.dropdown:not(.mega-menu-item) li {
  border-bottom: 1px solid $color-md-gray;
}

#mainMenu ul li.has-download.dropdown:not(.mega-menu-item) li:nth-last-child(2) {
  border-bottom: 0;
}

/*CIRCLE AROUND NAV ICONS*/

nav .circle-icon .fa {
  line-height: unset;
  margin-right: 0;
}

nav .circle-icon .fa {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 14px;
  height: 14px;
  border: 1px solid $color-dk-gray;
  padding: 0;
  line-height: 1.2;
  /*	margin-bottom: 2px;*/
  border-radius: 50%;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
}

.device-xxs nav .circle-icon .fa,
.device-xs nav .circle-icon .fa {
  margin-bottom: 3px;
}

nav .circle-icon .fa:before {
  font-size: 12px;
  position: absolute;
  top: 50%;
  left: 50%;
  border: 0;
  line-height: 1.1;
  -webkit-transform: translate(-45%, -51%);
  -moz-transform: translate(-45%, -51%);
  transform: translate(-45%, -51%);
}
.device-xxs nav .circle-icon .fa:before,
.device-xs nav .circle-icon .fa:before,
.device-sm nav .circle-icon .fa:before {
  -webkit-transform: translate(-45%, -12%);
  -moz-transform: translate(-45%, -12%);
  transform: translate(-45%, -12%);
}

#mainMenu > ul > li.circle-icon > a > span {
  line-height: 1.2;
  display: inline-block;
  vertical-align: middle;
  padding-left: 7px;
  /*	margin-bottom: 2px;*/
}

.device-xxs #mainMenu > ul > li.circle-icon > a > span,
.device-xs #mainMenu > ul > li.circle-icon > a > span {
  line-height: 1.1;
  margin-bottom: 2px;
}

/*=== Fix for Chrome Only ===*/
@media screen and(-webkit-min-device-pixel-ratio:0) {

  nav .circle-icon .fa {
    margin-bottom: 2px;
  }
  nav .circle-icon .fa:before {
    line-height: 1.2;
    margin: auto;
  }

  .device-xxs nav .circle-icon .fa:before,
  .device-xs nav .circle-icon .fa:before,
  .device-sm nav .circle-icon .fa:before {
