  #page-title {
    background-size: 100% !important;
  }
}


#page-title .container {
  position: relative;
}

#page-title [class*="col-"] {
  padding-left: 0;
  padding-right: 0;
}
#page-title a {
  color: inherit;
}
.page-title h1 {
  font-size: 56px;
  display: block;
  font-weight: 300;
  line-height: 1.2;
  margin-bottom: 10px;
  margin-left: -2px;
  margin-top: -4px;
