.single-post .page-title h1 {
  margin-bottom: 30px;
}

.stuning_page  h1{
  padding-top: 40px ;
  padding-bottom: 0 ;
}

.page-header-title-custom {
  padding-top: 40px
}

.page-title > span.lead {
  display: block;
  margin-top: 10px;
  font-weight: 300;
  font-size: 18px;
}

.page-title > span {
  opacity: 1;
}

/*page header right*/

.page-title-right .page-title {
  float: right;
  text-align: right;
}

.page-title-right .breadcrumb {
  text-align: left !important;
}

/*page title center */

.page-title-center .page-title {
  float: none;
  text-align: center;
  width: 100%;
}

.page-title-center .breadcrumb {
  float: none;
  text-align: center !important;
  width: 100%;
}

/*page title pattern*/

.page-title-pattern {
  background-attachment: fixed;
  background-repeat: repeat;
}

/*page title small */

#page-title.page-title-small {
  padding: 6px 0 4px !important;
}

#page-title.page-title-small .page-title {
  margin-top: 20px;
}

#page-title.page-title-small .page-title h1 {
  font-size: 20px;
  font-weight: 300;
  line-height: 20px;
  margin-bottom: 14px;
}

#page-title.page-title-small .breadcrumb {
  margin-top: 8px;
}

/*page title extended */

#page-title.page-title-extended {
  background-repeat: no-repeat;
  padding: 5% 0;
  background-position: center!important;
}

.canvas-dark #page-title {
  background: $color-dk-gray;
  background-repeat: no-repeat;
}

.page-title-extended h1 {
  font-size: 56px;
  letter-spacing: 1px;
  font-weight: 300;
  text-transform: none;
}

/*page title video */

#page-title.page-title-video {
  background-color: transparent;;
}

/*Page title: transparent header*/

#header.header-transparent + .page-title-video:before,
#header.header-transparent + .page-title-parallax:before,
#header.header-light-transparent + .page-title-video:before,
#header.header-light-transparent + .page-title-parallax:before,
#header.header-dark-transparent + .page-title-video:before,
#header.header-dark-transparent + .page-title-parallax:before{
  content: '';
  display: block;
  height: 90px;
}

#header.header-transparent + .page-title-video,
#header.header-transparent + .page-title-parallax,
#header.header-light-transparent + .page-title-video,
#header.header-light-transparent + .page-title-parallax,
#header.header-dark-transparent + .page-title-video,
#header.header-dark-transparent + .page-title-parallax {
  padding-top: 80px;
  padding-bottom: 80px;
}

.page-title-parallax {
  border-bottom: 5px solid $color-lt-gray !important;
  background-position: center !important;
}

@media (max-width: 991px) {
  #header.header-transparent + .page-title-video:before,
  #header.header-transparent + .page-title-parallax:before,
  #header.header-light-transparent + .page-title-video:before,
  #header.header-light-transparent + .page-title-parallax:before,
  #header.header-dark-transparent + .page-title-video:before,
  #header.header-dark-transparent + .page-title-parallax:before {
    display: none;
  }
  .canvas-dark #page-title {
    padding: 20%;
  }
}

@media (max-width: 500px) {
  .page-title span {
    display: none;
  }
  .single-post .page-title span {
    display: block;
  }
}

/* ----------------------------------------------------------------
    TOPBAR
-----------------------------------------------------------------*/

#topbar.topbar-transparent + #header.header-transparent + #slider,
#topbar.topbar-transparent + #header.header-transparent + .page-title-extended,
#topbar.topbar-transparent + #header.header-transparent + .page-title-video,
#topbar.topbar-transparent + #header.header-transparent + .page-title-parallax,
#topbar.topbar-transparent + #header.header-transparent + .fullscreen,
#topbar.topbar-transparent + #header.header-transparent + #google-map,
#topbar.topbar-transparent + #header.header-dark-transparent + #slider,
#topbar.topbar-transparent + #header.header-dark-transparent + .page-title-extended,
#topbar.topbar-transparent + #header.header-dark-transparent + .page-title-video,
#topbar.topbar-transparent + #header.header-dark-transparent + .page-title-parallax,
#topbar.topbar-transparent + #header.header-dark-transparent + .fullscreen,
#topbar.topbar-transparent + #header.header-dark-transparent + #google-map #topbar.topbar-transparent + #header.header-light-transparent + #slider,
#topbar.topbar-transparent + #header.header-light-transparent + .page-title-extended,
#topbar.topbar-transparent + #header.header-light-transparent + .page-title-video,
#topbar.topbar-transparent + #header.header-light-transparent + .page-title-parallax,
