#topbar {
  position: relative;
  z-index: 200;
  border-bottom: 1px solid $color-lt-gray;
  background-color: $color-white;
}

#topbar .top-menu {
  list-style: outside none none;
  margin: 0 -10px;
  padding: 0;
}

#topbar .top-links {
  position: relative;
}

#topbar .top-menu > li {
  display: inline-block;
  height: auto;
  line-height: 1;
  margin-bottom: 0;
}

#topbar .top-menu > li > a {
  display: block;
  font-size: 12px;
  font-weight: 600;
  height: auto;
  padding: 5px 10px;
  text-transform: uppercase;
  border-right: 1px solid $color-dk-gray;
}
#topbar .top-menu > li:last-child > a {
  border-right: none;
}
#topbar .top-menu > li i {
  font-size: 14px;
  margin-right: 10px;
}

#topbar .inline-list{
  display: inline-block;
  margin-right: 10px;
  vertical-align: middle;
}

#topbar .text-right {
  ul {
    float: left;
  }
}
#topbar .text-left {
  ul {
    float: right;
    li {
      padding: 16px 0;
      color: #fff;
      text-transform: uppercase;
      font-size: 13px;
    }
  }
}

#topbar .top-bar-close {
  position: absolute;
  right: 0;
  top: 14px;
  color: #fff;
  cursor: pointer;
}

/*Social icons*/

#topbar .social-icons {
  height: 100%;
  overflow: hidden;
  margin-bottom: 0;
}

#topbar .social-icons li,
#topbar .social-icons li a {
  float: left;
  list-style: outside none none;
}

#topbar .social-icons li a {
  border-radius: 0;
  border-right: 1px solid $color-dk-gray;
  font-size: 15px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  width: 35px;
  overflow: hidden;
  margin: 0;
}

#topbar .social-icons li:first-child a {
  border-left: 1px solid $color-dk-gray;
}

/*topbar dropdowns*/

.topbar-dropdown {
  color: $color-dk-gray;
  float: left;
  font-size: 13px;
  font-weight: 400;
  line-height: 40px;
  height: 40px;
  position: relative;
}

.topbar-dropdown .title {
  border-left: 1px solid $color-lt-gray;
  padding: 0 20px;
  cursor: pointer;
}

.topbar-dropdown:first-child .title {
  border-color: transparent;
  padding-left: 0;
}

.topbar-dropdown .title img,
.topbar-dropdown .list-entry img {
  display: inline-block;
  vertical-align: middle;
  margin-right: 7px;
  position: relative;
  top: -1px;
}

.topbar-dropdown .title a {
  color: $color-dk-gray;
}

.topbar-dropdown .title a:hover {
  text-decoration: underline;
}

.topbar-dropdown .title b {
  font-weight: 600;
}

.topbar-dropdown .title .fa {
  margin-left: 7px;
  position: relative;
  top: -1px;
}

.topbar-dropdown .title .fa:first-child {
  margin-left: 0;
  margin-right: 7px;
}

.topbar-dropdown .dropdown-list {
  position: absolute;
  width: 100%;
  left: 0;
  top: 40px;
  border-radius: 3px;
  background-color: $color-white;
  -webkit-box-shadow: 0 0 10px #1e1e1f;
  box-shadow: 0 0 10px #1e1e1f;
  border: solid 1px $color-lt-gray;
  padding: 0 16px;
  z-index: 1;
  text-align: left;
  opacity: 0;
  -moz-transition: all 300ms ease-out;
  -o-transition: all 300ms ease-out;
  -webkit-transition: all 300ms ease-out;
  transition: all 300ms ease-out;
  -ms-transition: all 300ms ease-out;
  transform-origin: 50% 0% 0;
  -moz-transform-origin: 50% 0% 0;
  -webkit-transform-origin: 50% 0% 0;
  -ms-transform-origin: 50% 0% 0;
  z-index: 1;
  visibility: hidden;
}

.topbar-dropdown:hover .dropdown-list,
.topbar-dropdown:hover .topbar-form {
  opacity: 1;
  transform: scale(1);
  -moz-transform: scale(1);
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  visibility: visible;
}

.topbar-dropdown .list-entry {
  white-space: nowrap;
  display: block;
  color: $color-dk-gray;
  padding: 2px 0;
  border-top: 1px $color-lt-gray solid;
}

.topbar-dropdown .list-entry:hover {
  color: #1e1e1f;
}

.topbar-dropdown .list-entry:first-child {
  border-top: none;
}

/*topbar form*/

.topbar-dropdown .topbar-form {
  position: absolute;
  width: 240px;
  padding: 20px 20px 10px 20px;
  right: auto;
  top: 40px;
  border-radius: 3px;
  background-color: $color-white;
  -webkit-box-shadow: 0 0 10px #1e1e1f;
  box-shadow: 0 0 10px #1e1e1f;
  border: solid 1px $color-lt-gray;
  z-index: 1;
  text-align: left;
  opacity: 0;
  -moz-transition: all 300ms ease-out;
  -o-transition: all 300ms ease-out;
  -webkit-transition: all 300ms ease-out;
  transition: all 300ms ease-out;
  -ms-transition: all 300ms ease-out;
  transform-origin: 50% 0% 0;
  -moz-transform-origin: 50% 0% 0;
  -webkit-transform-origin: 50% 0% 0;
  -ms-transform-origin: 50% 0% 0;
  visibility: hidden;
}

.topbar-dropdown input {
  margin-bottom: 2px;
}

.topbar-form .form-group {
  margin-bottom: 10px;
}

.topbar-form .checkbox {
  display: inline-block;
  margin-bottom: 0;
  margin-top: 0;
  vertical-align: middle;
  margin-top: -12px;
}

.topbar-dropdown.float-right .title {
  padding: 0 0 0 20px !important;
}

.topbar-dropdown .dropdown-invert.topbar-form {
  left: auto;
  right: -20px;
}

/*topbar fullwidth*/

body.wide #topbar.topbar-fullwidth > .container {
  width: 100%;
  padding: 0 30px;
}

/*dropdown topbar transparent*/

#topbar.topbar-dark .top-menu > li > a,
#topbar.topbar-dark .social-icons li a {
  color: $color-white !important;
}

#topbar.topbar-dark {
  background-color: #252525;
  border-color: $color-dk-gray;
}

#topbar.topbar-light .top-menu > li > a,
#topbar.topbar-light .social-icons li a {
  color: #1e1e1f !important;
}

/*topbar transparent*/

.topbar-transparent.topbar-dark .topbar-dropdown .title a,
.topbar-transparent.topbar-dark .topbar-dropdown {
  color: $color-white;
}

.topbar-transparent .topbar-dropdown .title {
  border-color: $color-dk-gray;
}

.topbar-transparent .topbar-dropdown:first-child .title {
  border-color: transparent;
}

#topbar.topbar-transparent {
  background-color: transparent !important;
  border-bottom: 1px solid $color-dk-gray;
}

#topbar.topbar-transparent .top-menu > li > a,
#topbar.topbar-transparent .social-icons li a {
  /*color: $color-lt-gray;*/
}

#topbar.topbar-dark.topbar-colored {
  border-bottom: 0;
}

/* ----------------------------------------------------------------
    Content
-----------------------------------------------------------------*/

#content {
  overflow: hidden;
  padding: 60px 0;
