/* ----------------------------------------------------------------------
	Accordion & Toggles
-------------------------------------------------------------------------*/
section {
  overflow: visible!important;
}

section.content {
  padding:0px;
}

.accordion .ac-item .ac-title:before {
  font-family: fontawesome;
}
.accordion {
  margin-bottom: 20px;
}
.accordion .ac-item .ac-title:before {
  cursor: pointer;
  position: absolute;
  top: 14px;
  right: 10px;
  display: block;
  padding: 3px 6px 2px;
  color: $color-lt-gray;
  content: "\f054";
  font-size: 12px;
  line-height: 12px;
  -webkit-transition: all 0.25s ease-in-out 0s;
  transition: all 0.25s ease-in-out 0s;
}
.accordion .ac-item.ac-active .ac-title:before {
  -webkit-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  transform: rotate(90deg);
  ms-transform: rotate(90deg);
}
.accordion .ac-item > .ac-title > i {
  margin-right: 16px;
}
.accordion .ac-title {
  position: relative;
  padding: 12px 30px 12px 15px;
  font-size: 18px;
  line-height: 24px;
  font-weight: 500;
  cursor: pointer;
  background-color: $color-dk-purple;
  margin-bottom: 2px;
  font-family: 'Roboto';
}
.accordion .ac-content {
  overflow: hidden;
}
.ac-content {
  padding: 16px 13px;
}

/*clean*/

.accordion.clean .ac-item .ac-title {
  border: 0px;
  background: transparent;
}

/*border*/

.accordion.border .ac-item .ac-title {
  border: 1px solid $color-lt-gray;
}
.accordion.border-bottom .ac-item .ac-title {
  border-bottom: 1px solid $color-lt-gray;
}

/*radius*/

.accordion.radius .ac-item .ac-title {
  border-radius: 4px;
}

/*fancy*/

.accordion.fancy .ac-item {
  -moz-border-bottom-colors: none;
  -moz-border-left-colors: none;
  -moz-border-right-colors: none;
  -moz-border-top-colors: none;
  background-color: $color-white;
  border-color: $color-lt-gray;
  border-style: solid;
  border-width: 1px 1px 0px 1px;
}
.accordion.fancy .ac-item .ac-title {
  margin: 0px;
}
.accordion.fancy .ac-item.ac-active .ac-title,
.accordion.fancy .ac-item:last-child {
  border-bottom: 1px solid $color-lt-gray;
}

/*fancy clean*/

.accordion.fancy.clean .ac-item.ac-active .ac-title {
  background-color: transparent;
}

/*fancy radius*/

.accordion.fancy.radius > .ac-item:first-child {
  border-radius: 4px 4px 0 0;
}
.accordion.fancy.radius > .ac-item:last-child {
  border-radius: 0 0 4px 4px;
}

/*color*/

.accordion.color .ac-item .ac-title:before {
  color: $color-white;
}
.accordion .ac-item.ac-open {
  display: block;
}

/*Accordion Transparent*/

.accordion.accordion-transparent .ac-item {
  background-color: transparent !important;
  border-width: 0 0 1px 0;
  border-style: solid;
  border-color: $color-dk-purple;
}
.accordion.accordion-transparent .ac-item .ac-title::before {
  color: $color-dk-purple;
}
.accordion.accordion-transparent.fancy .ac-item.ac-active .ac-title,
.accordion.accordion-transparent .ac-item:last-child {
  border-bottom: 1px solid $color-dk-purple;
}
.accordion.accordion-transparent .ac-item p {
  color: $color-dk-purple;
}

/* ----------------------------------------------------------------------
	Alert Messages
-------------------------------------------------------------------------*/

.alert > p {
  color: $color-white;
}
.alert.animated {
  z-index: 999999 !important;
}

