  color: $color-dk-purple;
  border: 1px solid $color-lt-gray;
}

/* Rounded */

.social-icons.social-icons-rounded li a {
  border-radius: 50%;
}

/* Medium Size */

.social-icons.social-icons-medium li a {
  height: 42px;
  width: 42px;
  line-height: 42px;
  font-size: 18px;
  margin: 0 10px 10px 0;
}

/* Large Size */

