/*---------------*/


/***** victor *****/


/*---------------*/

.image-box.effect.victor .image-box-content {
  -webkit-transition: all ease 0.6s;
  transition: all ease 0.6s;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.image-box.effect.victor:hover .image-box-content {
