/*---------------*/


/***** erdi *****/


/*---------------*/

.image-box.effect.erdi {
  background: #2f3238;
}
.image-box.effect.erdi img {
  max-width: none;
  -webkit-transition: opacity 1s, -webkit-transform 1s;
