  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}
.image-box.effect.erdi .image-box-content {
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.image-box.effect.erdi h3 {
  padding: 0.5em 0;
