  text-shadow: 1px 1px 1px $color-dk-purple;
}
.image-box.effect.erdi p {
  display: table;
  margin: 0 0 0.25em;
  padding: 0.4em 1em;
  background: $color-lt-gray;
  color: #2f3238;
  text-transform: none;
  font-weight: 500;
  font-size: 75%;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
  -webkit-transform: translate3d(-360px, 0, 0);
  transform: translate3d(-360px, 0, 0);
}
.image-box.effect.erdi p:first-child {
  -webkit-transition-delay: 0.15s;
  transition-delay: 0.15s;
}
.image-box.effect.erdi p:nth-of-type(2) {
  -webkit-transition-delay: 0.1s;
  transition-delay: 0.1s;
}
.image-box.effect.erdi p:nth-of-type(3) {
  -webkit-transition-delay: 0.05s;
  transition-delay: 0.05s;
}
.image-box.effect.erdi:hover p:first-child {
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
}
.image-box.effect.erdi:hover p:nth-of-type(2) {
  -webkit-transition-delay: 0.05s;
  transition-delay: 0.05s;
}
.image-box.effect.erdi:hover p:nth-of-type(3) {
  -webkit-transition-delay: 0.1s;
  transition-delay: 0.1s;
}
.image-box.effect.erdi:hover img {
  opacity: 0.4;
  -webkit-transform: scale3d(1.1, 1.1, 1);
  transform: scale3d(1.1, 1.1, 1);
}
.image-box.effect.erdi:hover p {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.image-box.effect.erdi p.image-box-links.image-box-links {
  background-color: transparent;
  margin-left: -15px;
}

/*-----------------*/


/***** tini *****/


/*-----------------*/

.image-box.effect.tini {
  background: #303fa9;
}
.image-box.effect.tini h3,
.image-box.effect.tini p {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
  -webkit-transform: translate3d(-50%, -50%, 0);
  transform: translate3d(-50%, -50%, 0);
  -webkit-transform-origin: 50%;
  -ms-transform-origin: 50%;
  transform-origin: 50%;
}
.image-box.effect.tini .image-box-content::before {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 170px;
  height: 170px;
  border: 2px solid $color-white;
  content: '';
  opacity: 0;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
  -webkit-transform: translate3d(-50%, -50%, 0) rotate3d(0, 0, 1, -45deg) scale3d(0, 0, 1);
  transform: translate3d(-50%, -50%, 0) rotate3d(0, 0, 1, -45deg) scale3d(0, 0, 1);
  -webkit-transform-origin: 50%;
  -ms-transform-origin: 50%;
  transform-origin: 50%;
}
.image-box.effect.tini p {
  width: 100px;
  text-transform: none;
  font-size: 140%;
  line-height: 2;
}
.image-box.effect.tini p a {
  color: $color-white;
  margin-right: 6px;
}
.image-box.effect.tini p a:hover,
.image-box.effect.tini p a:focus {
  opacity: 0.6;
}
.image-box.effect.tini p {
  padding-left: 10px;
}
.image-box.effect.tini p a i {
  opacity: 0;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
}
.image-box.effect.tini p a:first-child i {
  -webkit-transform: translate3d(-60px, -60px, 0);
  transform: translate3d(-60px, -60px, 0);
}
.image-box.effect.tini p a:nth-child(2) i {
  -webkit-transform: translate3d(60px, -60px, 0);
  transform: translate3d(60px, -60px, 0);
}
.image-box.effect.tini p a:nth-child(3) i {
  -webkit-transform: translate3d(-60px, 60px, 0);
  transform: translate3d(-60px, 60px, 0);
}
.image-box.effect.tini p a:nth-child(4) i {
  -webkit-transform: translate3d(60px, 60px, 0);
  transform: translate3d(60px, 60px, 0);
}
.image-box.effect.tini p a:nth-child(5) i {
  -webkit-transform: translate3d(60px, -60px, 0);
  transform: translate3d(60px, -60px, 0);
}
.image-box.effect.tini p a:nth-child(6) i {
  -webkit-transform: translate3d(-60px, -60px, 0);
  transform: translate3d(-60px, -60px, 0);
}
.image-box.effect.tini:hover .image-box-content::before {
  opacity: 1;
  -webkit-transform: translate3d(-50%, -50%, 0) rotate3d(0, 0, 1, -45deg) scale3d(1, 1, 1);
  transform: translate3d(-50%, -50%, 0) rotate3d(0, 0, 1, -45deg) scale3d(1, 1, 1);
}
.image-box.effect.tini .image-box-content {
  -webkit-transition: all ease 0.4s;
  transition: all ease 0.4s;
}
.image-box.effect.tini:hover .image-box-content {
  background: $color-dk-purple;
}
.image-box.effect.tini:hover h3 {
  opacity: 0;
  -webkit-transform: translate3d(-50%, -50%, 0) scale3d(0.8, 0.8, 1);
  transform: translate3d(-50%, -50%, 0) scale3d(0.8, 0.8, 1);
}
.image-box.effect.tini:hover p i:empty {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  /* just because it's stronger than nth-child */
  opacity: 1;
}

/*-----------------*/


/***** bleron *****/


/*-----------------*/

.image-box.effect.bleron {
  background: #df4e4e;
}
.image-box.effect.bleron img,
.image-box.effect.bleron h3 {
  -webkit-transition: -webkit-transform 0.35s;
  transition: transform 0.35s;
}
.image-box.effect.bleron img {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}
.image-box.effect.bleron h3 {
  text-shadow: 1px 1px 1px $color-dk-purple;
}
.image-box.effect.bleron h3,
.image-box.effect.bleron p {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 30px;
}
.image-box.effect.bleron p {
  text-transform: none;
  font-size: 90%;
  opacity: 0;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
  -webkit-transform: translate3d(0, 50px, 0);
  transform: translate3d(0, 50px, 0);
}
.image-box.effect.bleron:hover img {
  -webkit-transform: translate3d(0, -80px, 0);
  transform: translate3d(0, -80px, 0);
}
.image-box.effect.bleron:hover h3 {
  -webkit-transform: translate3d(0, -100px, 0);
  transform: translate3d(0, -100px, 0);
}
.image-box.effect.bleron:hover p {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.image-box.effect.bleron p.image-box-links a {
  background-color: $color-white;
}

/*----------------------*/


/***** effect-default *****/


/*----------------------*/

.effect.effect-default {
  text-align: left;
  position: relative;
}
.effect.effect-default img {
  -webkit-transition: opacity 0.35s;
  transition: opacity 0.35s;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}
.effect.effect-default h3 {
  -webkit-transition: -webkit-transform 0.25s;
  transition: transform 0.25s;
  -webkit-transform: translate3d(0, 20px, 0);
  transform: translate3d(0, 20px, 0);
}
.effect.effect-default p {
  display: inline;
  left: 0;
  margin-top: -20px;
  /* margin-left: -28px;*/
  position: absolute;
  text-align: center;
  top: 50%;
  width: 100%;
}
.effect.effect-default a i {
  margin: 0 4px;
  color: $color-white;
  font-size: 146%;
}
.effect.effect-default:hover img {
  opacity: 0.6;
}
.effect.effect-default:hover h3 {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.effect.effect-default:hover .image-box-content::before {
  opacity: 0.7;
  -webkit-transform: rotate3d(0, 0, 1, 20deg);
  transform: rotate3d(0, 0, 1, 20deg);
}
.effect.effect-default p a i {
  border-radius: 50%;
  height: 42px;
  width: 42px;
  line-height: 44px;
  background: hsla(0, 0%, 0%, 0.5);
  opacity: 0;
  -webkit-transition: opacity 0.25s, -webkit-transform 0.25s;
  transition: opacity 0.25s, transform 0.25s;
  -webkit-transform: translate3d(0, 30px, 0);
  transform: translate3d(0, 30px, 0);
}
.effect.effect-default:hover p i {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0) rotate(0deg);
  transform: translate3d(0, 0, 0) rotate(0deg);
}
.effect.effect-default:hover p a:nth-child(3) i {
  -webkit-transition-delay: 0.05s;
  transition-delay: 0.05s;
}
.effect.effect-default:hover p a:nth-child(2) i {
  -webkit-transition-delay: 0.1s;
  transition-delay: 0.1s;
}
.effect.effect-default:hover p a:first-child i {
  -webkit-transition-delay: 0.15s;
  transition-delay: 0.15s;
}
.effect.effect-default:hover p a i:hover {
  -webkit-transition: all 0.5s ease 0s;
  transition: all 0.5s ease 0s;
  -webkit-transition-delay: 1s;
  transition-delay: 1s;
  -webkit-transform: translate3d(0, 0, 0) rotate(360deg);
  transform: translate3d(0, 0, 0) rotate(360deg);
}
.effect .image-box-content p i {
  z-index: 2;
}

/*---------------*/


/***** Retro *****/


/*---------------*/

.image-box.effect.retro {
  background: #030c17;
  text-align: center;
}
.image-box.effect.retro img {
  opacity: 0.9;
  -webkit-transition: opacity 0.35s;
  transition: opacity 0.35s;
}

.image-box.effect.retro .image-box-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.image-box.effect.retro .image-box-content::before {
  position: absolute;
  top: 30px;
  right: 30px;
  bottom: 30px;
  left: 30px;
  border: 2px solid $color-white;
  box-shadow: 0 0 0 30px $color-dk-gray;
  content: '';
  opacity: 0;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
  -webkit-transform: scale3d(1.4, 1.4, 1);
  transform: scale3d(1.4, 1.4, 1);
}
.image-box.effect.retro h3 {
  margin: 5% 0 10px 0;
  text-shadow: 1px 1px 1px $color-dk-purple;
  -webkit-transition: -webkit-transform 0.35s;
  transition: transform 0.35s;
}
.image-box.effect.retro p {
  opacity: 0;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
  -webkit-transform: scale(1.5);
  -ms-transform: scale(1.5);
  transform: scale(1.5);
}
.image-box.effect.retro:hover h3 {
  -webkit-transform: scale(0.9);
  -ms-transform: scale(0.9);
  transform: scale(0.9);
}
.image-box.effect.retro:hover .image-box-content::before,
.image-box.effect.retro:hover p {
  opacity: 1;
  -webkit-transform: scale3d(1, 1, 1);
  transform: scale3d(1, 1, 1);
}
.image-box.effect.retro:hover .image-box-content {
  background-color: $color-dk-purple;
}
.image-box.effect.retro:hover img {
  opacity: 0.4;
}

