Font Awesome Icons
-------------------------------------------------------------------------*/

.fontawesome-icon-list i {
  font-size: 18px;
  display: inline-block;
  font-size: 1.3em;
  margin-right: 5px;
  position: relative;
  text-align: center;
  top: 2px;
  width: 30px;
  padding: 16px 0px;
}

/* ----------------------------------------------------------------------
Counters
-------------------------------------------------------------------------*/

.counter {
  margin-bottom: 20px;
  color: $color-md-purple;
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
}
.counter span {
  font-size: 50px;
  line-height: 40px;
}
.counter-hashtag span:before {
  content: "#";
}
.counter.small > span {
  font-size: 32px !important;
}
.counter.medium > span {
  font-size: 50px !important;
}
.counter.large > span {
  font-size: 62px !important;
}
.icon-box.fancy > .counter span {
  margin: 0 12px 10px 56px;
}
.icon-box.fancy.medium > .counter span {
  margin: 0 12px 10px 80px;
}
.icon-box.effect:not(.center) > .counter span {
  margin: 0 12px 10px 82px;
}
.icon-box.effect.large:not(.center) > .counter span {
  margin: 0 12px 10px 107px;
}
.icon-box.effect.medium:not(.center) > .counter span {
  margin: 0 12px 10px 82px;
}
.icon-box.effect.small:not(.center) > .counter span {
  margin: 0 12px 10px 58px;
  font-size: 32px !important;
}

/* ----------------------------------------------------------------------
	Text box
-------------------------------------------------------------------------*/

.text-box {
  margin-bottom: 32px;
}
.text-box > h4,
.text-box > h3 {
  margin-bottom: 10px;
  font-weight: 600;
}

/* ----------------------------------------------------------------------
Countdown timer
-------------------------------------------------------------------------*/

.countdown {
  color: $color-dk-purple;
  overflow: hidden;
}
.countdown-container {
  display: table;
  margin: 0 auto;
}
.countdown .countdown-box {
  display: block;
  float: left;
  margin: 30px;
  overflow: hidden;
  position: relative;
  text-align: center;
  width: auto;
}
.countdown .number {
  font-size: 80px;
  font-weight: 600;
  line-height: 1;
  margin-bottom: 10px;
  text-align: center;
}
.countdown span {
  display: block;
  font-family: "Open Sans", sans-serif;
  font-size: 21px;
  font-weight: 400;
  overflow: hidden;
  padding-top: 10px;
  position: relative;
  text-align: center;
}
.countdown span:before {
  border-bottom: 2px solid;
  content: "";
  display: block;
  left: 0;
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  right: 0;
  top: 0;
  width: 30px;
}

/*circle style*/

.countdown.circle .countdown-box {
  border: 3px solid ;
  border-radius: 50%;
  color: $color-dk-purple;
  height: 160px;
  padding: 34px 5px 0 0;
  width: 160px;
  margin: 20px 15px;
}
.countdown.circle .countdown-box .number {
  font-size: 52px;
  font-weight: 800;
}
.countdown.circle .countdown-box span {
  font-size: 18px;
}

/*rectangle style*/

.countdown.rectangle .countdown-box {
  border: 4px solid;
  padding: 24px;
  width: 170px;
  margin-left: 15px;
  margin-right: 15px;
}
.countdown.rectangle .countdown-box .number {
  font-size: 52px;
  font-weight: 600;
}
.countdown.rectangle .countdown-box span {
  font-size: 18px;
}

/*medium size*/

.countdown.medium .countdown-box,
.countdown.circle.medium .countdown-box,
.countdown.rectangle.medium .countdown-box {
  height: 130px;
  margin: 10px;
  padding: 20px 4px 0 0;
  width: 130px;
}
.countdown.circle.medium .countdown-box,
.countdown.rectangle.medium .countdown-box {
  border: solid 2px;
}
.countdown.medium .number,
.countdown.circle.medium .number,
.countdown.rectangle.medium .number {
  font-size: 44px;
  margin-bottom: 12px;
}
.countdown.medium span,
.countdown.circle.medium span,
.countdown.rectangle.medium span {
  font-size: 18px;
}

/*small size*/

.countdown.small .countdown-box,
.countdown.circle.small .countdown-box,
.countdown.rectangle.small .countdown-box {
  margin: 10px;
  height: 70px;
  padding: 10px 0px 0 0;
  width: 70px;
  margin: 5px 5px;
}
.countdown.circle.small .countdown-box,
.countdown.rectangle.small .countdown-box {
  border: solid 2px;
}
.countdown.small .number,
.countdown.circle.small .number,
.countdown.rectangle.small .number {
  font-size: 16px;
  margin-bottom: 6px;
}
.countdown.small span,
.countdown.circle.small span,
.countdown.rectangle.small span {
  font-size: 14px;
  padding-top: 2px;
}

/*Light & Dark*/

.countdown.light .countdown-box {
  border-color: $color-white;
  color: $color-white;
  text-shadow: 1px 1px 0 $color-dk-purple;
}
.countdown.countdown-light span:before {
  background: $color-white;
}
.countdown.countdown-light span,
.countdown.countdown-light .number {
  color: $color-white !important;
}
.countdown.countdown-light.circle .countdown-box {
  border-color: $color-white;
}
.countdown.countdown-light.rectangle .countdown-box {
  border-color: $color-white;
}
.countdown.countdown-dark span,
.countdown.countdown-dark .number {
  color: $color-dk-purple !important;
}

/* ----------------------------------------------------------------------
ProgressBar
-------------------------------------------------------------------------*/

.progress-bar-container {
  width: 100%;
  height: 40px;
  margin: 20px 0 20px 0;
  overflow: hidden;
  background-color: $color-lt-gray;
}
.progress-bar {
  height: 100%;
  padding: 10px;
  background: $color-dk-purple;
  box-shadow: none;
}
.progress-title {
  color: $color-white;
  font-size: 13px;
  font-weight: 600;
  position: absolute;
  text-align: left;
}
.progress-title > i {
  margin-right: 8px;
}
.progress-bar .progress-number,
.progress-bar .progress-type {
  color: $color-white;
  float: right;
  margin-top: -2px;
}

/*radius style*/

.progress-bar-container.radius,
.progress-bar-container.radius .progress-bar {
  border-radius: 4px;
}

/*title-up style*/

.progress-bar-container.title-up .progress-title {
  color: $color-dk-purple;
  margin-left: -10px;
  margin-top: -32px;
}
.progress-bar-container.title-up:not(:first-child) {
  margin-top: 40px;
}
.progress-bar-container.title-up .progress-bar .progress-number,
.progress-bar-container.title-up .progress-bar .progress-type {
  color: $color-dk-purple;
  margin-top: -32px;
  position: absolute;
  right: 15px;
}
.progress-bar-container.title-up .progress-bar .progress-number,
.progress-bar-container.title-up .progress-bar .progress-type {
  color: $color-dk-purple;
  margin-top: -32px;
  position: absolute;
  right: 15px;
}
.progress-bar-container.title-up .progress-bar .progress-number {
  right: 26px;
}

/*medium size*/

.progress-bar-container.medium {
  height: 20px;
}
.progress-bar-container.medium .progress-bar {
  padding: 0px;
}
.progress-bar-container.medium .progress-title {
  margin-left: 0;
  margin-top: -20px;
}
.progress-bar-container.medium .progress-bar .progress-number,
.progress-bar-container.medium .progress-bar .progress-type {
  margin-top: -18px;
}

/*small size*/

.progress-bar-container.small {
  height: 10px;
}
.progress-bar-container.small .progress-bar {
  padding: 0px;
}
.progress-bar-container.small .progress-title {
  margin-left: 0;
  margin-top: -20px;
}
.progress-bar-container.small .progress-bar .progress-number,
.progress-bar-container.small .progress-bar .progress-type {
  margin-top: -18px;
}

/*progress bar no-bg*/

.progress-bar-container.no-bg {
  background: none !important;
}

/*progrerss extra small*/

.progress-bar-container.extra-small {
  height: 2px;
}
.progress-bar-container.extra-small .progress-bar {
  padding: 0px;
}
.progress-bar-container.extra-small .progress-title {
  margin-left: 0;
  margin-top: -20px;
}
.progress-bar-container.extra-small .progress-bar .progress-number,
.progress-bar-container.extra-small .progress-bar .progress-type {
  margin-top: -18px;
}

/*transparent progrerss bar*/

.progress-bar-container.transparent {
  background: none !important;
}
.progress-bar-container.transparent .progress-bar {
  background-color: $color-dk-purple;
}
.progress-bar-container.transparent.title-up .progress-bar .progress-title,
.progress-bar-container.transparent.title-up .progress-bar .progress-number,
.progress-bar-container.transparent.title-up .progress-bar .progress-type {
