/* ----------------------------------------------------------------------
Pie charts
-------------------------------------------------------------------------*/

.pie-animated {
  opacity: 1;
}
.pie-chart {
  display: inline-block;
  margin-bottom: 20px;
  margin-top: 50px;
  position: relative;
  text-align: center;
}
.pie-chart canvas {
  position: absolute;
  top: 0;
  left: 0;
}
.pie-chart span,
.pie-chart i {
  display: inline-block;
  font-size: 28px;
  z-index: 2;
  font-weight: 600;
}
.pie-chart i {
  font-size: 38px;
}
.pie-chart span.percent:after {
  content: '%';
  margin-left: 0.1em;
  font-size: .8em;
}

/*transparent*/

.pie-chart.transparent span,
.pie-chart.transparent span.percent,
.pie-chart.transparent i {
  color: $color-white;
}

/* ----------------------------------------------------------------------
Horisontal Rules
-------------------------------------------------------------------------*/

hr {
  border-top: 1px solid $color-md-gray;
  margin-bottom: 20px;
  margin-top: 10px;
  clear: both;
}

hr.space {
  border: none;
  height: 50px;
}
hr.space-xs {
  border: none;
  height: 20px;
}
hr.space-md {
  border: none;
  height: 40px;
}
hr.space-lg {
  border: none;
  height: 80px;
}
hr.space-xlg {
  border: none;
  height: 120px;
}
hr.space-xxlg {
  border: none;
  height: 200px;
}
body.device-sm hr.space {
  height: 26px;
}
body.device-xs hr.space,
body.device-xxs hr.space {
  height: 16px;
}
body.device-xxs hr.space-lg,
body.device-xs hr.space-lg {
  height: 40px;
}
body.device-xxs hr.space-xlg,
body.device-xs hr.space-xlg {
  height: 40px;
}
body.device-xxs hr.space-xxlg,
body.device-xs hr.space-xxlg {
  height: 80px;
}
.space {
  width: 100%;
  display: block;
  clear: both;
  height: 20px;
  position: relative;
}

/* ----------------------------------------------------------------------
    Delimiter
-------------------------------------------------------------------------*/


.custom-heading-delim {
  border-top-color: #84c340 !important;
}

/* ----------------------------------------------------------------------
    Seperator
-------------------------------------------------------------------------*/


.seperator,
.separator{
  color: $color-md-gray;
  width: 100%;
  margin: 30px auto;
  overflow: hidden;
  text-align: center;
  line-height: 1.2em;
}
.seperator-simple {
  margin: 10px auto;
}

.seperator::before,
.seperator::after,
.separator::before,
.separator::after{
  border-bottom: 1px solid $color-md-gray;
  content: "";
  display: inline-block;
  height: 0.65em;
  margin: 0 -4px 0 -100%;
  vertical-align: top;
  width: 50%;
}


.vc_separator.vc_sep_color_green .vc_sep_line {
  border-color: $color-lt-lime-green;
}

.seperator.seperator-dark::before,
.seperator.seperator-dark::after,
.separator.seperator-dark::before,
.separator.seperator-dark::after{
  border-bottom: 1px solid $color-dk-purple !important;
}
.seperator.seperator-grey::before,
.seperator.seperator-grey::after,
.separator.seperator-grey::before,
.separator.seperator-grey::after{
  border-bottom: 1px solid $color-lt-gray;
}
.seperator.seperator-light::before,
.seperator.seperator-light::after,
.separator.seperator-light::before,
.separator.seperator-light::after {
  border-bottom: 1px solid $color-dk-gray;
}
.seperator::after,
.separator::after{
  margin: 0 -100% 0 0;
}
.seperator i,
.separator i {
  font-size: 18px;
}
.seperator i,
.seperator span,
.seperator a,
.separator i,
.separator span,
.separator a{
  margin: 0 20px 0 24px;
  display: inline-block;
}

/*dotted style*/

.seperator.dotted:before,
.seperator.dotted:after,
.separator.dotted:before,
.separator.dotted:after{
  border-bottom: 2px dotted $color-lt-gray;
}

/*seperator left icon*/

.seperator.seperator-left,
.separator.seperator-left {
  float: left;
  margin: 2px 36px 0 0;
}
.seperator.left i,
.separator.left i{
  float: left;
  margin: 2px 36px 0 0;
}

/*seperator right icon*/

.seperator.right i,
.separator.right i {
  float: right;
  margin: 0 0 2px 36px;
}

/*seperator bold*/

.seperator.seperator-bold:before,
.seperator.seperator-bold:after,
.separator.seperator-bold:before,
.separator.seperator-bold:after{
  border-bottom-width: 6px;
}

/*seperator sizes*/

.seperator.seperator-small,
.separator.seperator-small {
  width: 10%;
  margin: 5px auto;
}
.seperator.seperator-medium,
.separator.seperator-medium{
  width: 20%;
  margin: 10px auto;
}
.text-seperator {
  background-color: $color-dk-purple;
  display: block;
  height: 4px;
  margin: 20px 0;
}
.text-seperator-small {
  width: 10%;
}
.text-seperator-medium {
  width: 30%;
}
.text-seperator-large {
  width: 60%;
}

.seperator.seperator-image:before,
.seperator.seperator-image:after,
.separator.seperator-image:before,
.separator.seperator-image:after {
  border: 0;
}
.seperator.seperator-image,
.separator.seperator-image {
  background-position: center center;
  background-repeat: repeat-x;
  margin: 0;
  padding: 0;
}
.seperator.seperator-over-top,
.separator.seperator-over-top {
  margin-bottom: -17px;
  position: relative;
  top: -13px !important;
}

.triangle-divider-bottom {
  background-image: url("../images/triangle-divider-down.png");
  background-position: left center;
  background-repeat: repeat-x;
  bottom: 0;
  height: 12px;
  position: absolute;
  width: 100%;
}
.triangle-divider-top {
  background-image: url("../images/triangle-divider-top.png");
  background-position: left center;
  background-repeat: repeat-x;
  top: 0;
  height: 12px;
  position: absolute;
  width: 100%;
}
/* ----------------------------------------------------------------------
Blockquotes
-------------------------------------------------------------------------*/

.blockquote-simple {
  border: 0 none !important;
}
.blockquote-fancy,
.blockquote-color,
.blockquote-dark {
  border: 0 none !important;
}
.blockquote-fancy::before {
  color: $color-dk-purple;
  display: block;
  font-size: 100px;
  left: 4px;
  position: absolute;
  top: 14px;
}
.blockquote-fancy > small {
  float: right;
}
.blockquote-dark {
  background-color: $color-dk-purple;
}
.blockquote-color,
.blockquote-dark {
  padding: 28px;
  color: $color-white;
}

/* ----------------------------------------------------------------------
Forms
-------------------------------------------------------------------------*/

.form-control {
  border-radius: 0;
  box-shadow: none;
  height: auto;
  line-height: 1.33333;
  padding: 12px 18px;
}
.form-control:focus {
  box-shadow: none;
}
.form-control.input-lg {
  resize: none;
  font-size: .9em
}
.form-control.rounded {
  border-radius: 4px
}
.form-control,
.input-group-addon {
  border: 1px solid $color-lt-gray;
}
.form-group label {
  color: #444;
  cursor: pointer;
  display: inline-block;
  margin-top: 8px;
  margin-bottom: 4px;
  text-transform: uppercase;
  font-size: 13px;
}
.form-inline button {
  margin: 0;
}
.form-group > label {
  display: block;
}
.has-feedback label ~ .form-control-feedback {
  top: 44px;
}
.form-gray-fields .form-control {
  background-color: #f2f2f2;
  border-color: $color-lt-gray;
  color: $color-dk-purple
}
.form-gray-fields .form-control:focus {
  background-color: $color-lt-gray
}
.form-transparent-fields .form-control {
  background-color: $color-dk-purple;
  border-color: $color-dk-purple;
}
.form-transparent-fields .form-control:focus {
  background-color: $color-dk-purple;
}
.form-transparent-light-fields .form-control {
  background-color: $color-dk-gray;
  border-color: $color-md-gray;
}
.form-transparent-light-fields .form-control:focus {
  background-color: $color-md-gray;
}
label.error {
  color: #b71828;
  display: none !important;
  font-weight: 400;
}
.form-control.error,
.sm-form-control.error {
  border-color: #b71828;
}
.form-control.error {
  color: #b71828;
}
.form-control::-moz-placeholder {
  color: $color-md-gray;
}
.form-control::-webkit-input-placeholder {
  color: $color-md-gray;
}

/*form select*/

select {
  border: 1px solid $color-lt-gray;
  color: $color-md-gray;
  cursor: pointer;
  display: block;
  line-height: 14px;
  max-width: 100%;
  min-width: 100%;
  padding: 14px;
}
.order-select > h6 {
  margin-bottom: 0;
}
.order-select p {
  font-size: 13px;
}
.button-search {
  background-color: $color-white;
  border-color: $color-lt-gray $color-lt-gray $color-lt-gray;
  border-style: solid;
  border-width: 1px 1px 1px 0 !important;
  box-shadow: none;
  margin-left: -6px !important;
  z-index: 2 !important;
}

/* ----------------------------------------------------------------------
 Reservation form
-------------------------------------------------------------------------*/

.reservation-form-over {
  top: -80px;
  z-index: 10;
  margin-bottom: -80px;
  overflow: inherit;
}
.reservation-form {
  background-color: $color-white;
  border-radius: 3px;
  border: 1px solid $color-lt-gray;
  min-height: 160px;
  padding: 30px;
  position: relative;
  z-index: 9999 !important;
}
.reservation-form label {
  color: $color-dk-gray;
}
.reservation-form input,
.reservation-form select {
  border: 0px;
  border-radius: 4px;
  background-color: $color-white;
  border: 2px solid $color-lt-gray;
  width: 100%;
}
.reservation-form .date input {
  border-radius: 4px 0 0 4px;
  border-width: 2px 0 2px 2px;
}
.reservation-form .input-group-addon {
  -moz-border-bottom-colors: none;
  -moz-border-left-colors: none;
  -moz-border-right-colors: none;
  -moz-border-top-colors: none;
  background-color: $color-white !important;
  border-color: $color-lt-gray;
  border-image: none;
  border-style: solid;
  border-width: 2px 2px 2px 0;
}
.reservation-form input,
.reservation-form select {
  color: $color-dk-gray;
  display: block;
  font-size: 15px;
  line-height: 1.42857;
  padding: 8px 14px;
  width: 100%;
}

/*reservation form style 2*/

#book {
  background-color: $color-dk-purple;
  border: 10px solid $color-dk-gray;
  border-radius: 3px;
  padding: 40px;
}
#book label {
  color: $color-white;
}
#book .date input {
  border-radius: 4px 0 0 4px;
}
#book input,
#book select {
  color: $color-dk-gray;
  display: block;
  font-size: 15px;
  line-height: 1.42857;
  padding: 8px 14px;
  width: 100%;
}
#book .input-group-addon {
  background-color: $color-white !important;
  border: 0 none;
}
#book input,
#book select {
  border: 0 none;
}

/* ----------------------------------------------------------------------
Labels & Badgets
-------------------------------------------------------------------------*/

.label-default {
  background: -webkit-linear-gradient(top, $color-md-orange 0px, $color-lt-orange 100%) repeat scroll 0 0 $color-lt-orange;
  background: linear-gradient(to bottom, $color-md-orange 0px, $color-lt-orange 100%) repeat scroll 0 0 $color-lt-orange;
  color: $color-white;
}
.main-menu .label,
.sidebar-menu .label {
  padding: 1px 3px;
  margin: 0 3px;
}
.badge {
  margin-right: 10px;
}

/* ----------------------------------------------------------------------
Lightbox
-------------------------------------------------------------------------*/

.mfp-ajax-holder {}
.lightbox-open {
  position: relative;
  overflow: hidden !important;
}
.mfp-ajax-holder .mfp-content {
  background: $color-lt-gray;
  max-width: 1000px;
}
.mfp-ajax-holder .ajax-quick-view {
  padding: 20px 30px 35px;
}
.mfp-ajax-holder .ajax-quick-view .quick-view-title {
  border-bottom: 1px solid $color-lt-gray;
  margin-bottom: 30px;
  position: relative;
}
.mfp-ajax-holder .ajax-quick-view .quick-view-title h2 {
  font-weight: 600;
}
.mfp-ajax-holder .ajax-quick-view .quick-view-content {}
.mfp-ajax-holder .mfp-close {
  background: none repeat scroll 0 0 $color-white !important;
  margin-right: -45px;
  margin-top: 0;
}

/* ----------------------------------------------------------------------
Lists
-------------------------------------------------------------------------*/

dl,
ol,
ul {
  padding-bottom: 0;
  padding-left: 20px;
}
ol {
  padding-left: 1.6em
}
ol li {
  padding-left: 6px;
}
.list-unstyled ul li {
  list-style: none;
}

#main-content ul {
  margin-bottom: 30px;
}

.list {
  clear: both;
  display: block;
  position: relative;
}
.list-lines,
.list-icons,
.list-simple,
.list-posts,
.list ul {
  padding: 0px;
}

.list-chevron ul {
  margin-left: 10px;
}

.list,
.list-lines,
.list-icons,
.list-simple,
.list-posts,
.list-tweets li {
  list-style: none;
}
.list-icons li,
.list-arrow-icons li,
.list-tweets li {
  padding-left: 22px;
}
.list-arrow-icons li:before {
  margin-left: -18px;
  position: absolute;
  font-family: fontawesome;
  content: "\f105";
}
.list-chevron li:before {
  margin-left: -28px;
  position: absolute;
  content: "\e960";
  font-family: 'icomoon' !important;
  font-size: 10px;
  margin-top: 7px;
}
.widget-tweeter li:before,
.widget-twitter li:before {
  margin-left: -18px;
  position: absolute;
  font-family: fontawesome;
  content: "\f099";
}
.list-tweets .list-tweets-date,
.widget-tweeter .tweets_txt > span,
.widget-twitter .tweets_txt > span {
  display: block;
  font-size: 12px;
  font-style: italic;
  opacity: 0.8;
}
.list-icons li i {
  font-size: 14px;
  margin-left: -22px;
  margin-right: 6px;
  top: 0;
}
.list-lines li {
  border-bottom: 1px solid $color-lt-gray;
  padding: 10px 0 10px 2px;
}
.list-lines li:last-child {
  border-bottom: 0px;
}
.background-dark .list-lines li {
  border-bottom: 1px solid $color-dk-gray;
}
.list-medium li {
  margin: 12px 0;
}
.list-large li {
  margin: 18px 0;
}
.list-large li:first-child,
.list-medium li:first-child {
  margin-top: 0;
}
.list-large li:last-child,
.list-medium li:last-child {
  margin-bottom: 0;
}
.list-posts small {
  display: block;
  font-style: italic;
  opacity: 0.7;
}
.list-check {
  list-style: outside none none;
  margin-bottom: 20px;
}
.list-check li {
  margin-bottom: 10px;
}
.list-check li:last-child {
  margin-bottom: 0;
}
.list-check li::before {
  color: $color-dk-purple;
  content: "\f00c";
  display: inline-block;
  font-family: fontawesome;
  font-size: 18px;
  height: 20px;
  line-height: 19px;
  margin-right: 10px;
  padding-left: 2px;
  text-align: center;
  vertical-align: middle;
  width: 20px;
}
.list-check.default li::before {
  border: 2px solid $color-lt-orange;
  color: $color-md-orange;
}
.list-check.blue li::before {
  border: 2px solid $color-md-robin-blue;
  color: $color-md-robin-blue;
}
.list-check.green li::before {
  border: 2px solid $color-lt-lime-green;
  color: $color-lt-lime-green;
}
.list-arrow {
  list-style: outside none none;
  margin-bottom: 20px;
  padding: 0px;
}
.list-arrow li {
  margin-bottom: 10px;
}
.list-arrow li:last-child {
  margin-bottom: 0;
}
.list-arrow li::before {
  border-radius: 50%;
  color: $color-dk-purple;
  display: inline-block;
  font-family: fontawesome;
  content: "\f18e";
  font-size: 18px;
  height: 20px;
  line-height: 19px;
  margin-right: 10px;
  padding-left: 2px;
  text-align: center;
  vertical-align: middle;
  width: 20px;
}

/* custom list bullets and styling */

.ss-order-list ol {
  list-style-type: none;
  margin-left: 0;
  padding-left: 0;
}

.ss-order-list ol::first-child {
  counter-reset: customlistcounter;
}

.ss-order-list ol > li {
  counter-increment: customlistcounter;
  font-size: 1em;
  line-height: 1.5;
  margin: 0 20px 20px 40px;
  text-indent: -44px;
}

.ss-order-list ol > li::before {
  background-color: #fff;
  border: 1px solid $color-md-lime-green;
  border-radius: 10em;
  content: counter(customlistcounter) "";
  font-size: .9em;
  font-weight: bold;
  margin-right: .4em;
  padding: .4em .8em;
}

.ss-order-list li::last-child {
  margin-bottom: 0;
}

.ss-order-list ul::before {
  color: #000;
  content: "";
}

.ss-order-list ul {
  font-size: 1em;
  margin-left: 3em;
  padding: 5px;
}

.ss-order-list ul > li {
  line-height: .8em;
}



