/* ----------------------------------------------------------------------
Pagination & Pager
-------------------------------------------------------------------------*/

.pagination-wrap {
  display: block;
  clear: both;
}
.pagination > li > a,
.pagination > li > span {
  color: $color-md-gray;
  padding: 8px 14px;
}
.pagination i {
  font-size: 18px;
  color: $color-md-gray;
}

/*pagination simple style*/

.pagination.pagination-simple {
  margin: 0;
}
.pagination.pagination-simple > li > a,
.pagination.pagination-simple > li > span {
  border: 0;
  background-color: transparent !important;
}
.pagination.pagination-simple > li > a:hover i,
.pagination.pagination-simple > li > a:hover,
.pagination.pagination-simple > li > span:hover,
.pagination.pagination-simple > li > a:focus,
.pagination.pagination-simple > li > span:focus {
  background-color: transparent !important;
  border-color: transparent !important;
  color: $color-dk-purple;
}
.pagination.pagination-simple > .active > a,
.pagination.pagination-simple > .active > span,
.pagination.pagination-simple > .active > a:hover,
.pagination.pagination-simple > .active > span:hover,
.pagination.pagination-simple > .active > a:focus,
.pagination.pagination-simple > .active > span:focus {
  background: $color-dk-purple;
  color: $color-dk-gray;
  font-size: 130%;
  font-weight: 600;
  padding-top: 3px;
}

/*pagination fancy style*/

.pagination.pagination-fancy > li a {
  border-bottom: 3px solid rgba(0, 0, 0, 0.08);
}
.pagination.pagination-fancy > li:first-child > a,
.pagination-fancy > li:first-child > span {
  border-bottom-left-radius: 20px;
  border-top-left-radius: 20px;
  margin-left: 0;
}
.pagination.pagination-fancy > li:last-child > a,
.pagination-fancy > li:last-child > span {
  border-bottom-right-radius: 20px;
  border-top-right-radius: 20px;
  margin-right: 0;
}

/*pagination rounded style*/

.pagination.pagination-rounded > li > a,
.pagination.pagination-rounded > li > span {
  padding: 8px 14px 9px;
  border-radius: 100%;
}
.pagination.pagination-rounded > li > a,
.pagination-rounded > li > span {
  border-radius: 50%;
  margin: 0 2px;
}
.pagination.pagination-rounded > li:first-child > a
{
  background: none !important;
  border: 0 none !important;
  margin-right: 10px;
  padding-top: 10px;
}
.pagination.pagination-rounded > li:last-child > a {
  background: none !important;
  border: 0 none !important;
  margin-left: 10px;
  padding-top: 10px;
}

/*PAGER*/

.pager > li > a,
.pager > li > span {
  color: $color-md-gray;
  padding: 8px 14px;
}
.pager i {
  font-size: 18px;
  color: $color-white;
}
.pager.pager-fancy > li a {
  background-color: $color-lt-lime-green;
  border-color: $color-lt-lime-green;
  color: $color-white;
  text-shadow: 1px 1px 1px $color-dk-purple;
}
.pager.pager-fancy > li a:hover {
  opacity: 0.9;
}
.pager.pager-fancy > li:first-child > a,
.pager-fancy > li:first-child > span {
  border-radius: 6px 0 0 6px;
  margin-left: 0;
}
.pager.pager-fancy > li:last-child > a,
.pager-fancy > li:last-child > span {
  border-radius: 0 6px 6px 0;
  margin-right: 0;
}
.pager.pager-fancy > li:last-child > a i,
.pager-fancy > li:last-child > span i {
  margin-left: 6px;
}
.pager.pager-fancy > li:first-child > a i,
.pager-fancy > li:first-child > span i {
  margin-right: 6px;
}
.pager.pager-rounded > li a {
  background-color: $color-lt-lime-green;
  border-color: $color-lt-lime-green;
  color: $color-white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.pager.pager-rounded > li a:hover {
  opacity: 0.9;
}
.pager.pager-rounded > li:last-child > a i,
.pager-rounded > li:last-child > span i {
  margin-left: 6px;
}
.pager.pager-rounded > li:first-child > a i,
.pager-rounded > li:first-child > span i {
  margin-right: 6px;
}

/*navigations*/

.pager-modern {
  background-color: $color-lt-gray;
  text-transform: uppercase;
  width: 100%;
  margin: 0;
}
.pager-modern i {
  margin: 0 10px;
  vertical-align: middle;
}
.pager-modern a,
.pager-modern a:visited {
  color: $color-dk-gray;
}
.pager-modern a:hover {
  color: $color-dk-purple;
}
.pager-modern a span {
  cursor: pointer;
  display: inline-block;
  height: 60px;
  line-height: 59px;
  padding: 0 4%;
  -webkit-transition: all 0.2s ease-out 0s;
  transition: all 0.2s ease-out 0s;
}
.pager-modern span:hover {
  background-color: $color-md-gray;
}
.pager.pager-modern i {
  color: $color-dk-purple;
}
.pager-prev {
  text-align: left;
}
.pager-all {
  text-align: center;
}
.pager-all i {
  font-size: 38px;
}
.pager-next {
  text-align: right;
}

/* ----------------------------------------------------------------------
Sections
-------------------------------------------------------------------------*/

section {
  position: relative;
  z-index: auto;
  width: 100%;
  overflow: hidden;
}

/* ----------------------------------------------------------------------
Panels
-------------------------------------------------------------------------*/

.panel .table th,
.panel .table td {
  padding-left: 20px !important;
}


/* ----------------------------------------------------------------------
Breadcrumbs
-------------------------------------------------------------------------*/

.breadcrumb {
  background-color: transparent;
  font-size: 12px;
  line-height: 1.3em;
  margin-bottom: 1.5em;
  margin-top: 16px;
  text-align: left;
}
.breadcrumb ul {
  display: inline;
  margin: 0;
  padding: 0;
}
.breadcrumb li {
  display: inline
}
.breadcrumb a {
  color: $color-dk-gray;
}
.breadcrumb li + li:before {
  content: "\f105";
  padding: 0 10px;
  font-family: FontAwesome;
}

/*fancy style*/

.breadcrumb.fancy {
  overflow: hidden;
  font-size: 12px;
  line-height: 1.3em;
  background-color: transparent;
  padding: 0;
  margin-bottom: 20px;
  margin-top: 14px;
}
.breadcrumb.fancy li {
  line-height: 32px;
  margin: 0 2px 0 10px;
}
.breadcrumb.fancy li + li:before,
.breadcrumb.radius li + li:before {
  content: none;
}
.breadcrumb.fancy li:first-child i {
  margin-right: -6px;
}
.breadcrumb.fancy a {
  background: $color-lt-gray;
  padding: .7em 1em;
  text-decoration: none;
  position: relative;
}
.breadcrumb.fancy a:hover,
.breadcrumb.fancy .active a {
  background: $color-lt-gray;
  color: $color-dk-purple;
}
.breadcrumb.fancy a::before {
  content: "";
  position: absolute;
  top: 50%;
  margin-top: -1.5em;
  border-width: 1.5em 0 1.5em 1.1em;
  border-style: solid;
  border-color: $color-lt-gray $color-lt-gray $color-lt-gray transparent;
  left: -1em;
}
.breadcrumb.fancy li:first-child {
  margin: 0 2px 0 0;
}
.breadcrumb.fancy li:first-child a::before {
  border-width: 0;
}
.breadcrumb.fancy a:hover::before,
.breadcrumb.fancy .active a:before {
  border-color: $color-lt-gray $color-lt-gray $color-lt-gray transparent;
}
.breadcrumb.fancy a::after {
  content: "";
  position: absolute;
  top: 50%;
  margin-top: -1.5em;
  border-top: 1.5em solid transparent;
  border-bottom: 1.5em solid transparent;
  border-left: 1em solid $color-lt-gray;
  right: -1em;
}
.breadcrumb.fancy a:hover::after,
.breadcrumb.fancy .active a:after {
  border-left-color: $color-lt-gray;
}

/*radius breadcrumb*/

.breadcrumb.radius {
  background: transparent;
  overflow: hidden;
  font-size: 12px;
}
.breadcrumb.radius li {
  margin-right: 16px;
}
.breadcrumb.radius a:hover,
.breadcrumb.radius .active a {
  background: $color-lt-gray;
  color: $color-dk-purple;
}
.breadcrumb.radius a,
.breadcrumb.radius .active a {
  background: $color-lt-gray;
  padding: 4px 8px;
  text-decoration: none;
  position: relative;
}
.breadcrumb.radius a:hover {
  background: $color-lt-gray;
}
.breadcrumb.radius a::before,
.breadcrumb.radius a::after {
  background: none repeat scroll 0 0 $color-lt-gray;
  bottom: 0;
  content: "";
  height: 25px;
  position: absolute;
  top: 0;
  -webkit-transform: skew(-8deg);
  -ms-transform: skew(-8deg);
  transform: skew(-8deg);
  width: 10px;
}
.breadcrumb.radius a::before {
  left: -.5em;
  border-radius: 5px 0 0 5px;
}
.breadcrumb.radius a:hover::before {
  background: $color-lt-gray;
}
.breadcrumb.radius a::after {
  right: -.5em;
  border-radius: 0 5px 5px 0;
}
.breadcrumb.radius a:hover::after,
.breadcrumb.radius .active:hover {
  background: $color-lt-gray;
}
.breadcrumb.radius a:hover::after,
.breadcrumb.radius .active a:after {
  border-left-color: $color-lt-gray;
}
body.device-sm .breadcrumb,
