/*columns*/

.portfolio [data-isotope-col="1"] .portfolio-item {
  margin-bottom: 50px !important;
  border-bottom: 1px solid $color-lt-gray;
  padding-bottom: 46px;
}
body.device-lg .portfolio [data-isotope-col="1"] .portfolio-item .portfolio-image,
body.device-md .portfolio [data-isotope-col="1"] .portfolio-item .portfolio-image {
  width: 50%;
  float: left;
}
body.device-lg .portfolio [data-isotope-col="1"] .portfolio-item .portfolio-description,
body.device-md .portfolio [data-isotope-col="1"] .portfolio-item .portfolio-description {
  padding-left: 22px;
  width: 35%;
}
body.device-lg .portfolio [data-isotope-col="1"] .portfolio-item .portfolio-date,
body.device-md .portfolio [data-isotope-col="1"] .portfolio-item .portfolio-date {
  padding-right: 22px;
  width: 15%;
}
body.device-lg .portfolio [data-isotope-col="1"] .portfolio-item .portfolio-details,
body.device-md .portfolio [data-isotope-col="1"] .portfolio-item .portfolio-details {
  float: left;
  padding: 0 22px;
  width: 50%;
  clear: none;
}
body.device-lg .portfolio [data-isotope-col="1"] .portfolio-item .portfolio-description h4,
body.device-md .portfolio [data-isotope-col="1"] .portfolio-item .portfolio-description h4 {
  font-size: 26px;
  margin-bottom: 3px;
}
body.device-lg .portfolio [data-isotope-col="5"] .portfolio-item .portfolio-description h4,
body.device-md .portfolio [data-isotope-col="5"] .portfolio-item .portfolio-description h4 {
  font-size: 14px;
}
body.device-lg .portfolio [data-isotope-col="6"] .portfolio-item .portfolio-description h4,
body.device-md .portfolio [data-isotope-col="6"] .portfolio-item .portfolio-description h4 {
  font-size: 12px;
}

/* Portfolio - Filter
-----------------------------------------------------------------*/

.portfolio-filter {
  padding: 0;
  margin-bottom: 20px;
  display: none;
  &.displayFilter {
    display: block;
  }
}
.portfolio-filter li,
.portfolio-filter .filter-active-title {
  border-radius: 4px;
  cursor: pointer;
  display: block;
  font-size: 13px;
  font-weight: 600;
  line-height: 18px;
  padding: 16px;
  position: relative;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  color: $color-dk-purple;
}

.portfolio-filter li.js-widget-title {
  cursor: default;
}

/*Responsive filter*/

body.device-sm .portfolio-filter,
body.device-xs .portfolio-filter ,
body.device-xxs .portfolio-filter{
  &.displayFilter {
    display: none;
  }
}

body.device-xs .portfolio-filter li,
body.device-xxs .portfolio-filter li {
  display: block;
  width: 50%;
  float: left;
  border-width: 0px;
}
.filter-active-title {
  float: right;
  padding: 10px 0;
  position: relative;
}
body:not(.device-lg):not(.device-md) .filter-active-title {
  display: none;
}
.portfolio-filter.portfolio-filter-transparent,
.portfolio-filter.portfolio-filter-transparent li {
  color: $color-white;
}
.portfolio-filter.portfolio-filter-transparent li.ptf-active,
.portfolio-filter.portfolio-filter-transparent .portfolio-filter li:hover,
.portfolio-filter.portfolio-filter-transparent .filter-active-title,
.portfolio-filter.portfolio-filter-transparent li.ptf-active,
.portfolio-filter.portfolio-filter-transparent li:hover {
  background-color: transparent !important;
  border-radius: 0px;
  opacity: 0.6;
}

/*Portfolio Filter Classic */
.portfolio-filter.portfolio-filter-classic {
  margin-bottom: 20px;
}
.portfolio-filter.portfolio-filter-classic li,
.portfolio-filter.portfolio-filter-classic li.ptf-active,
.portfolio-filter.portfolio-filter-classic li:hover {
  background: none;
  color: $color-dk-purple !important;
  padding: 6px 0;
  margin-right: 20px;
}

.portfolio-filter.portfolio-filter-classic li:after {
  background: $color-dk-purple;
  content: "";
  height: 2px;
  left: 0;
  opacity: 0;
  position: absolute;
  top: 100%;
  transform: translateY(10px);
  transition: opacity 0.3s ease 0s, transform 0.3s ease 0s;
  width: 100%;
}
.portfolio-filter.portfolio-filter-classic li:hover:after,
.portfolio-filter.portfolio-filter-classic li:focus:after,
.portfolio-filter.portfolio-filter-classic li.ptf-active:after {
  opacity: 1;
  transform: translateY(0px);
}

