-------------------------------------------------------------------------*/

.portfolio-basic-image {
  margin-bottom: 40px;
}

/* ----------------------------------------------------------------------
Portfolio Carousel
-------------------------------------------------------------------------*/

.portfolio-client-info ul {
  border-bottom: 1px solid #E7E7E7;
  padding-bottom: 20px;
}
.portfolio-client-info ul li {
  overflow: hidden;
}
.portfolio-client-info ul li span {
  float: left;
  font-size: 13px;
  font-weight: 700;
  margin-right: 10px;
}
.portfolio-client-info ul li a {
  color: $color-dk-purple;
}
.portfolio-client-description {
  margin-top: 20px;
}

/* ----------------------------------------------------------------
    Text Rotater
-----------------------------------------------------------------*/

.text-rotator > span {
  display: none;
}
.text-rotator > .animated {
  display: inline-block;
  opacity: 1;
}

/*MISC*/

.side-image {
  padding: 0;
  position: relative;
}
.side-image .image-container {
  overflow: hidden;
  position: absolute;
  height: 100%;
  padding: 0px;
  top: 0px;
}
.side-image .image-container.pull-right {
  right: 0px;
}
.side-image .content {
  padding-top: 80px;
  padding-bottom: 80px;
}
.side-image p.lead {
  margin-bottom: 40px;
}
.feature-icon-left {
  float: left;
  margin-bottom: 40px;
  overflow: hidden;
}
.feature-icon-left .icon-holder {
  float: left;
  width: 45px;
}
.feature-icon-left i {
  font-size: 32px;
}
.feature-icon-left .feature-text {
  float: left;
  padding-top: 8px;
  width: 217px;
}
.boxed-grid .row {
  margin-right: -10px;
  margin-left: -10px;
}
.boxed-grid [class*="col-"] {
  padding-left: 10px;
  padding-right: 10px;
}
.boxed-grid [class*="col-"] a {
  margin-bottom: 20px;
}
.boxed-grid a {
  overflow: hidden;
  display: block;
  position: relative;
}
.boxed-grid a img {
  width: 100%;
  height: auto;
  transition: all 0.6s ease;
}
.boxed-grid a:hover img {
  transform: scale(1.2);
}
.boxed-grid a .title {
  position: absolute;
  top: 40%;
  left: 30px;
  font-size: 28px;
  font-weight: 800;
  text-transform: uppercase;
  color: $color-white;
}
.text-center a .title,
.text-right a .title {
  width: 100%;
  left: 0;
}
.text-right a .title {
  left: auto;
  right: 30px;
  width: 100%;
}

/*MISC 2*/

.background-image {
  background-position: center top;
  background-size: cover;
}
.background-fixed {
  background: fixed center top;
  background-size: cover;
}
.parallax,
.page-title-parallax,
.background-fixed {
  background-size: cover;
  background: fixed center center;
  position: relative;
  overflow: hidden;
}

.page-title-pattern {
  background: repeat fixed;
  position: relative;
  overflow: hidden;
}

.background-overlay-gradient > .container,
.background-overlay-gradient-dark > .container,
.background-overlay > .container,
.background-overlay-one > .container,
.background-overlay-dark > .container,
.background-overlay-light > .container,
.background-overlay-grey > .container,
.background-overlay-grey-dark > .container,
.background-overlay-dark > .container,
#particles-js > .container,
.background-overlay-gradient > .container-fluid,
.background-overlay-gradient-dark > .container-fluid,
.background-overlay > .container-fluid,
.background-overlay-one > .container-fluid,
.background-overlay-dark > .container-fluid,
.background-overlay-light > .container-fluid,
.background-overlay-grey > .container-fluid,
.background-overlay-grey-dark > .container-fluid,
.background-overlay-dark > .container-fluid,
#particles-js > .container-fluid {
  position: relative;
}
.background-overlay-gradient:before {
  background: -moz-linear-gradient(top, $color-md-purple 0%, rgba(255, 255, 255, 0) 99%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, $color-md-purple), color-stop(99%, rgba(255, 255, 255, 0)));
  background: -webkit-linear-gradient(top, $color-md-purple 0%, rgba(255, 255, 255, 0) 99%);
  background: -o-linear-gradient(top, $color-md-purple 0%, rgba(255, 255, 255, 0) 99%);
  background: -ms-linear-gradient(top, $color-md-purple 0%, rgba(255, 255, 255, 0) 99%);
  background: linear-gradient(to bottom, $color-md-purple 0%, rgba(255, 255, 255, 0) 99%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#96000000', endColorstr='#00ffffff', GradientType=0);
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  content: " ";
  width: 100%;
  height: 100%;
  z-index: 0;
  opacity: 0.8;
}
.background-overlay-gradient-dark:before {
  background: -moz-linear-gradient(top, $color-dk-purple 0%, $color-dk-purple 100%);
  background: -webkit-linear-gradient(top, $color-dk-purple 0%, $color-dk-purple 100%);
  background: linear-gradient(to bottom, $color-dk-purple 0%, $color-dk-purple 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='$color-dk-purple00000', endColorstr='#fc101010', GradientType=0);
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  content: " ";
  width: 100%;
  height: 100%;
  z-index: 0;
  opacity: 0.0;
}
.background-overlay:before {
  background: url("../../library/img/overlay-pattern/overlay-pattern-1.png");
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  content: " ";
  width: 100%;
  height: 100%;
  z-index: 0;
  opacity: 0.0;
}
.background-overlay-one:before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: $color-dk-purple;
  content: " ";
  width: 100%;
  height: 100%;
  z-index: 0;
}
.background-overlay-dark:before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: $color-dk-purple;
  content: " ";
  width: 100%;
  height: 100%;
  z-index: 0;
  opacity: 0.0;
}
.background-overlay-light:before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: $color-md-gray;
  content: " ";
  width: 100%;
  height: 100%;
  z-index: 0;
  opacity: 0.0;
}
.background-overlay-grey:before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: $color-dk-purple;
  content: " ";
  width: 100%;
  height: 100%;
  z-index: 0;
  opacity: 0.0;
}
.background-overlay-grey-dark:before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: $color-dk-gray;
  content: " ";
  width: 100%;
  height: 100%;
  z-index: 0;
  opacity: 0.0;
}
#particles-js {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: transparent;
  background: $color-dk-purple;
  z-index: 0;
}

/* ----------------------------------------------------------------------
Responsive utilities
-------------------------------------------------------------------------*/

.responsive-utilities th small {
  color: #999;
  display: block;
  font-weight: 400;
}
.responsive-utilities td.is-visible {
  background-color: #dff0d8 !important;
  color: #468847;
}

/* ----------------------------------------------------------------------
	SHOP
-------------------------------------------------------------------------*/

.shop {
  position: relative;
}
.product {
  margin-bottom: 40px;
  position: relative;
  display: block;
}
.product .product-image {
  position: relative;
  width: 100%;
  height: auto;
  overflow: hidden;
}
.product .product-image > a,
.product .product-image img {
  display: block;
  width: 100%;
}
.product .product-image > a,
.product .product-wishlist a {
  -webkit-transition: all .4s ease;
  -o-transition: all .4s ease;
  transition: all .4s ease;
}
.product .product-image > a:nth-of-type(2) {
  top: 0;
  left: 0;
  opacity: 0;
  z-index: -1;
  position: absolute;
}
.product .product-image:hover > a:nth-of-type(2) {
  opacity: 1;
  z-index: 2;
}
.product .product-wishlist a {
  border: 2px solid $color-dk-purple;
  border-radius: 50%;
  height: 30px;
  left: 12px;
  line-height: 28px;
  position: absolute;
  text-align: center;
  top: 12px;
  width: 30px;
  z-index: -1;
  opacity: 0;
}
.product:hover .product-wishlist,
.product:hover .product-wishlist a {
  z-index: 3;
  opacity: 1;
}
.product .product-description {
  padding: 8px 0 10px;
}
.product-title {
  min-height: 35px;
  width: 80%;
}
.product .product-title h3,
.product .product-title h3 a {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 0;
}
.product .product-category {
  display: block;
  opacity: 0.5;
}
.product .product-price {
  width: 26%;
  float: right;
  margin-bottom: 0;
  margin-top: -26px;
  text-align: right;
}
.product .product-price ins {
  color: #444;
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 4px;
  text-decoration: none;
}
.product .product-price del {
  display: block;
  font-size: 12px;
  font-weight: 400;
  margin: -20px 0 -4px;
  opacity: 0.6;
}
.product .product-rate {
  height: 23px;
  float: left;
  color: #FFC300;
}
.product .product-reviews,
.product .product-reviews a {
  color: #73848e;
  float: right;
  font-size: 13px;
}
.product .product-sale,
.product .product-new,
.product .product-hot,
.product .product-out-stock {
  border-radius: 50%;
  color: $color-white;
  font-size: 11px;
  font-weight: 600;
  height: 42px;
  right: 10px;
  line-height: 42px;
  position: absolute;
  text-align: center;
  text-shadow: 1px 2px 1px $color-dk-purple;
  top: 10px;
  width: 42px;
  z-index: 5;
}
.product .product-sale,
.product .product-sale-off {
  background: #ffc300;
}
.product .product-new {
  background: $color-lt-lime-green;
}
.product .product-hot {
  background: #d6284b;
}
.product .product-out-stock {
  background: #a5a5a5 none repeat scroll 0 0;
  border-radius: 4px;
  padding: 0 18px;
  width: auto;
}
.product .product-sale-off {
  border-radius: 12px 0 0;
  bottom: 0px;
  color: $color-white;
  font-size: 16px;
  font-weight: 600;
  height: 42px;
  line-height: 42px;
  position: absolute;
  right: 0px;
  text-align: center;
  text-shadow: 1px 2px 1px $color-dk-purple;
  width: 88px;
  z-index: 5;
}
.product .product-overlay {
  position: absolute;
  width: 100%;
  height: 44px;
  line-height: 44px;
  top: auto;
  bottom: -44px;
  left: 0;
  z-index: 6;
}
.product .product-overlay,
.product .product-overlay a {
  -webkit-transition: bottom .3s ease;
  -o-transition: bottom .3s ease;
  transition: bottom .3s ease;
}
.product:hover .product-overlay {
  bottom: 0;
}
.product .product-overlay a {
  color: $color-white;
  display: block;
  font-size: 13px;
  background-color: $color-dk-purple;
  text-align: center;
}
.product .product-overlay a:hover {
  background-color: $color-dk-purple;
  color: $color-white;
  text-shadow: 1px 1px 1px $color-dk-purple;
}

/*Shop 6 columns*/

body:not(.device-xs):not(.device-xxs):not(.device-sm) .col-md-2 .product .product-title h3 {
  font-size: 15px;
  line-height: 16px;
}
body:not(.device-xs):not(.device-xxs):not(.device-sm) .col-md-2 .product {
  margin-bottom: 85px;
}
body:not(.device-xs):not(.device-xxs):not(.device-sm) .col-md-2 .product .product-price {
  display: block;
  float: left;
  margin-top: 10px;
  width: 100%;
  text-align: left;
}
body:not(.device-xs):not(.device-xxs):not(.device-sm) .col-md-2 .product .product-price del {
  display: unset;
  margin: -20px 7px 4px 0;
}
body:not(.device-xs):not(.device-xxs):not(.device-sm) .col-md-2 .product .product-reviews {
  float: left;
}

/*Shop categories (boxes)*/

.shop-category {
  position: relative;
}
.shop-category-box {
  position: relative;
  margin-bottom: 20px;
}
.shop-category-box img {
  width: 100%;
}
.shop-category-box .shop-category-box-title {
  background-color: $color-lt-gray;
  bottom: 10px;
  padding: 4px 0;
  position: absolute;
  text-align: center;
  width: 100%;
}
.shop-category-box-title h6 {
  margin-bottom: 0;
}

/*Shop Cart*/

.shop-cart th,
.shop-cart .table td {
  vertical-align: middle;
}
.shop-cart .cart-product-remove {
  text-align: center;
}
.shop-cart .cart-product-thumbnail > a {
  float: left;
  margin-right: 12px;
}
.shop-cart .cart-product-thumbnail img {
  max-width: 52px;
}
.shop-cart .cart-product-thumbnail .cart-product-thumbnail-name {
  padding: 24px;
  font-weight: 600;
}
.cart-product-description {
  max-width: 200px;
  font-size: 12px;
}
.cart-product-description span {
  display: block;
}
.cart-product-quantity .minus,
.cart-product-quantity .plus {
  border: 1px solid $color-lt-gray;
  margin: 0 -4px;
  padding: 5px 12px;
}
.cart-product-quantity .qty {
  border: 1px solid $color-lt-gray;
  max-width: 40px;
  padding: 5px;
  text-align: center;
}
.payment-method td {
  padding: 12px 20px !important;
}

/*Shop product Page*/

.product-page:not(.widget-shop) .product-title h3 {
  font-size: 28px;
  font-weight: 600;
  margin-bottom: 0;
}
.product-size {
  display: block;
  list-style: outside none none;
  margin: 0 20px 15px 0;
  overflow: hidden;
  padding-left: 0;
}
.product-size li {
  float: left;
  margin: 0 6px 0 0;
}
.product-size li label span {
  cursor: pointer;
  display: block;
  font-size: 1.2em;
  font-weight: 400;
  height: 40px;
  line-height: 37px;
  margin: 0;
  text-align: center;
  transition: all 0.3s ease 0s;
  width: 40px;
}
.product-size li label input {
  left: -9000px;
  position: absolute;
}
.product-size li label span {
  border: 1px solid $color-lt-gray;
  color: #909090;
}
.product-size li label span:hover {
  border-color: #909090;
}
.product-size li label input:checked + span {
  background-color: $color-lt-gray;
  border-color: $color-lt-gray;
}
.product .comment .product-rate {
  float: right;
}
.product .comment {
  margin-top: 20px;
}
.ajax-quick-view .product {
  margin-bottom: 0px;
  margin-top: 20px;
}

/*Shop widget*/

.widget-shop {
  position: relative;
}
.widget-shop .product {
  position: relative;
  border-bottom: 1px solid $color-lt-gray;
  float: left;
  margin-bottom: 12px;
  min-height: 96px;
  padding-bottom: 12px;
}
.widget-shop .product:last-child {
  border-bottom: 0;
}
.widget-shop .product .product-description {
  padding-top: 0px;
}
.widget-shop .product .product-image {
  float: left;
  padding-right: 10px;
  width: 26%;
}
.widget-shop .product .product-description {
  display: table-cell;
  padding-bottom: 0;
  vertical-align: top;
  width: 74%;
}
.widget-shop .product .product-title,
.widget-shop .product .product-category {
  font-size: 12px;
  margin-bottom: 5px;
  margin-top: -8px;
}
.widget-shop .product .product-title h3,
.widget-shop .product .product-title h3 a {
  font-size: 15px;
  line-height: 18px;
}
.widget-shop .product .product-description .product-price {
  clear: both;
  float: none;
  margin-bottom: 0;
  margin-top: 0;
  text-align: left;
  width: 100%;
}
.widget-shop .product .product-description .product-price ins {
  font-size: 14px;
  margin-bottom: 4px;
}
.widget-shop .product .product-description .product-price del {
  display: unset;
  font-size: 12px;
  margin: -8px 5px -8px 0;
}
.widget-tweeter .list-tweets {
  padding-left: 0;
}
.widget-categories .list {
  padding: 0;
}
#footer .widget-categories a {
  color: $color-dk-gray;
}

/*shop box*/

.shop-promo-box {
  min-height: 360px;
  background-repeat: no-repeat;
  padding: 50px;
  border: 8px solid $color-lt-gray;
}
.shop-promo-box > h2 {
  font-weight: 800;
  margin-bottom: 0;
}

/* ----------------------------------------------------------------------
	LARGE BOXES
-------------------------------------------------------------------------*/

.box-fancy > .container {
  padding: 0 30px;
}
.box-fancy [class*="col-"] {
  padding: 5% !important;
}
body.device-xxs .box-fancy [class*="col-"] {
  padding: 10% !important;
}
.box-fancy.fancy-box-over {
  top: -100px;
}
.over-slider-top {
  top: -100px;
}

/* ----------------------------------------------------------------------
    SITE MAP
-------------------------------------------------------------------------*/

.site-map i {
  margin-right: 8px;
}
.site-map ul li {
  padding-bottom: 2px;
}

/* ----------------------------------------------------------------
    ANIMATIONS
-----------------------------------------------------------------*/

.animated.infinite {
  animation-duration: 3s;
  animation-fill-mode: both;
}

/* ----------------------------------------------------------------
    YT PLAYER (YOUTUBE PLAYER)
-----------------------------------------------------------------*/

#youtube-background-controls {
  background: none;
  border: 2px solid $color-white;
  border-radius: 50%;
  height: 60px;
  position: relative;
  width: 60px;
}
#youtube-background-controls .fa {
  font-size: 23px;
  line-height: 40px;
}
#youtube-background-controls .fa-pause,
#youtube-background-controls .fa-play {
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
  opacity: 0;
  position: absolute;
  left: 19px;
  top: 9px;
}
#youtube-background-controls .fa-play {
  left: 22px;
}
#youtube-background-controls .fa-pause {
  opacity: 1;
}
#youtube-background-controls.video-is-playing .fa-play {
  opacity: 1;
}
#youtube-background-controls.video-is-playing .fa-pause {
  opacity: 0;
}

/* ----------------------------------------------------------------
    INSPIRO SLIDER
-----------------------------------------------------------------*/

.inspiro-slider.slider-fullscreen,
.inspiro-slider.slider-fullwidth {
  width: 100%;
  height: 100%;
}
.inspiro-slider,
.inspiro-slider .owl-item {
  padding: 0;
  width: 100%;
  height: auto;
  display: table;
  overflow: hidden;
  position: relative;
  table-layout: fixed;
  z-index: 0;
}
.inspiro-slider .owl-stage-outer {
  height: 100%;
}
.inspiro-slider .slide {
  width: 100%;
  height: 100%;
  display: table-cell;
  position: relative;
  vertical-align: middle;
  z-index: 2;
  background-position: 50% 20%;
  background-repeat: repeat-y;
  background-size: cover;
}

.inspiro-slider .slide-captions h1,
.inspiro-slider .slide-captions h2,
.inspiro-slider .slide-captions h3,
.inspiro-slider .slide-captions h4,
.inspiro-slider .slide-captions h5,
.inspiro-slider .slide-captions h6 {
  color: $color-white;
  font-family: "Open Sans";
  font-weight: 600;
  letter-spacing: -0.5px;
}
.inspiro-slider .slide-captions h1 {
  font-size: 80px;
  font-weight: 700;
  line-height: 80px;
  margin-bottom: 1%;
}
.inspiro-slider .slide-captions h2 {
  font-size: 60px;
  font-weight: 700;
  line-height: 60px;
  margin-left: -4px;
  margin-bottom: 10px;
}
.inspiro-slider .slide-captions .strong {
  color: $color-white;
  display: block;
  font-family: "Open Sans";
  font-size: 12px !important;
  font-weight: 700;
  letter-spacing: 2px;
  line-height: 12px;
  margin-bottom: 20px;
  position: relative;
  text-transform: uppercase;
}
.inspiro-slider .slide-captions .strong:after {
  border-top-color: $color-white;
  border-top-style: solid;
  border-top-width: 3px;
  content: "";
  display: block;
  margin-bottom: 0;
  margin-top: 8px;
  width: 26px;
}
.inspiro-slider .slide-captions .text-dark.strong:after,
.inspiro-slider .slide-captions.text-dark .strong:after {
  border-top-color: $color-dk-purple;
}
.inspiro-slider .slide-captions.text-center .strong:after {
  margin-left: auto;
  margin-right: auto;
}
.inspiro-slider .slide-captions.text-right .strong:after {
  margin-left: auto;
  margin-right: 0;
}
.inspiro-slider .slide-captions .slide-caption-hide {
  opacity: 0;
  visibility: hidden;
}

/*slider nav arrows*/

.inspiro-slider.owl-carousel .owl-controls .owl-nav .owl-next,
.inspiro-slider.owl-carousel .owl-controls .owl-nav .owl-prev {
  background-color: transparent;
  border: 1px solid $color-dk-gray;
  height: 78px;
  margin: 10px;
  width: 50px;
}
.inspiro-slider.owl-carousel .owl-controls .owl-nav .owl-next,
.inspiro-slider.owl-carousel .owl-controls .owl-nav .owl-prev {
  background-color: transparent;
  border: 1px solid $color-dk-gray;
  border-radius: 3px;
  color: $color-white;
  height: 78px;
  margin: 10px;
  width: 76px;
}
.inspiro-slider.owl-carousel .owl-controls .owl-nav .owl-next:hover,
.inspiro-slider.owl-carousel .owl-controls .owl-nav .owl-prev:hover {
  background-color: $color-white;
  border: 1px solid $color-dk-gray;
  color: $color-dk-purple;
  opacity: 1;
}

/*slider nav dots*/

.inspiro-slider .owl-dots {
  bottom: 4px;
  display: block;
  left: 0;
  position: absolute;
