/*** END of the CSS from the WP Page Editor for the Schedule Demo Agency Page ***/
/****************************************************************************************/
@media screen and (max-width: 767px){
  .social-icons ul {
    margin: 0 -4px;
  }
  .social-icons li {
    margin-left: 4px;
    margin-right: 4px;
  }
  .social-icons li a {
    margin: 0;
  }
}

@media screen and (max-width: 768px) {
  .list-chevron li:before {
    margin-top: 3px;
  }
  .vertical-center {
    display: block;
  }
  .zap-templates p {
    font-size: 16px;
    text-align: center;
  }
}

/*-------------------------------------------------------------*/
/* Style for no-hover behavior used to ensure no behavior happens on icons*/
.noHover{
  pointer-events: none;
}
/*-------------------------------------------------------------*/

/* Get A Demo */

@media (max-width: 768px) {
  .stacked-testimonials {
    .a-grid-3,
    .a-grid-4,
    .a-grid-5,
    .a-grid-6,
    .a-grid-7,
    .a-grid-8,
    .a-grid-9,
    .a-grid-10 {
      width: 30%;
    }
  }
  .hide-on-mobile {
    display: none;
  }
}

@media (min-width: 769px) {
  .hide-on-desktop {
    display: none;
  }
}
/*-------------------------------------------------------------*/
/* Investors Homepage Green Up-Arrow for NASDAQ */

ul.stock_ticker li span.sqitem:before {
  display: none;
}

/*-------------------------------------------------------------*/
/* Style only for small Polo Icon Box   */
/* You need to add class extra-small-icon to the box in which you
add the Polo Icon Box of type small to make it extra small */

.extra-small-icon .icon-box.border.small {
  .icon {
    height: 30px !important;
    top: 0px !important;
    width: 30px !important;
    i {
      font-size: 20px !important;
      line-height: 28px !important;
    }
  }
}

/*------------------------------------------------------------------------------------------*/
/* Style to ensure the redio button font type is not affected by the Smart Countdown plugin */
#demo {
  font-family: "Roboto", Helvetica, Arial, sans-serif;
}

/* width_fixer forces the theme's content container full with to prevent header flash and is removed from the element by child.js after document ready */
.content>div.container.width_fixer {
  width: 100% !important;
}

/*------------------------------------------------------------------------------------------*/
/* Style for the gototop button so that it does not overlap with the chat/help button  */
.gototop-button {
  box-shadow: 0 2px 6px 0 rgba(0,0,0,.4);
  border-radius: .3125rem;
  width: 54px;
  height: 54px;
  padding: 15px 5px 14px 17px;
  right: 35px;
  bottom: 105px;
}

@media screen and (max-width: 768px) {
  .gototop-button {
    right: 35px;
    bottom: 105px;
    padding: 15px 5px 14px 19px;
  }
  .page-template-landing-page .gototop-button {
    display: none !important;
  }
}

/*-------------------------------------------------------------*/

/* Hiding  SS Chatbot for Mobile Devices */

@media (max-width: 768px) {
  iframe#ssChatbot {
    display: none !important;
  }
}

/*-------------------------------------------------------------*/
/* Style for h2 Element */
@media screen and (min-width: 992px) {
  .font-size-30px h2 {
    font-size: 30px !important;
  }
}

@media screen and (max-width: 991px) {
  .font-size-30px h2 {
    font-size: 18px !important;
  }
}
/*-------------------------------------------------------------*/
